import React, { Component } from "react";
import PropTypes from "prop-types";
import "./TimeLine.scss"
class TimeLine extends Component {
    static defaultProps = {
        mainNode:false
    };
    render() {
        const { mainNode } = this.props;
        const childTail = mainNode?(<span className="timeline-item-tail-child">
        </span>):null
        return (
            <div className="timeLine">
                <div className={ !mainNode?'timeline-item-tail sub':'timeline-item-tail main'} >
                {
                   childTail
                }
                </div>
                <div className="timeline-item-head" />
                <div className={!mainNode?'timeline-item-content sub':'timeline-item-content main'}
                 >
                    {this.props.children}
                </div>
            </div>
        )
    }
}
TimeLine.propTypes = {
    mainNode: PropTypes.bool.isRequired
}

export default TimeLine
