
import {getBrands, getSeries, getStyles,getHkscode, queryPart} from './api.js'
import {
  BRAND_LIST,
  SERIES_LIST,
  STYLE_LIST,
  CAR_INFO,
  PARTS_LIST,
  VIN_CAR_LIST
} from "./actionTypes.js"
import { createActions } from 'redux-actions';


function brandList(list){
  return {type:BRAND_LIST,payload:list}
}
function seriesList(list){
  return {type:SERIES_LIST,payload:list}
}
function styleList(list){
  return {type:STYLE_LIST,payload:list}
}
function vinCarList(list){
    return {type:VIN_CAR_LIST,payload:list}
}
export function getCarInfo(item){
 return {type:CAR_INFO,payload:item?item:null}
}
export function getPartsList(list){
    return {type:PARTS_LIST,payload:list}
}
//获取品牌
export function getBrandList(){
  return (dispatch,getState)=>{
    console.log(getState())
    getBrands().then(res=>{
      console.log(res)
      if(res.status==200&&res.data.errcode==0){
          let dataList=res.data.modelbrandList;
          let character=[]
          let obj={
            data:[],
            letter:'热搜'
          }
          dataList.forEach(item=>{
              if(item.data.length){
                item.data.forEach(cars=>{
                    if(cars.ishot==1){
                        obj.data.push(cars)
                    }
                })
              }
          })
          dataList.unshift(obj)
          dataList.forEach(item=>{
            character.push(item.letter)
          })
          console.log(dataList)
          
          dispatch(brandList({character,dataList}))
      }
    })
  }
}
// 获取车系
export function getSeriesList(params){
  return (dispatch,getState)=>{
    getSeries(params).then(res=>{
      if(res.status==200&&res.data.errcode==0){
        let list = res.data.vehiclemodelList
        let car=[]
        list.forEach((item, index) => {
          if (index == 0) {
              car.push({
                  makename: item.makename,
                  model: [item]
              });
          } else {
              let myindex = car.findIndex(val => {
                  return val.makename == item.makename;
              });
              //没有的厂家填充
              if (myindex == -1) {
                  car.push({
                      makename: item.makename,
                      model: [item]
                  });
              } else {
                  //有该厂家，重复判断
                  let shouldPushCar = !car[myindex].model.some(
                      val =>
                          val.model == item.model &&
                          val.vehicletype == item.vehicletype
                  );
                  if (shouldPushCar) {
                      car[myindex].model.push(item);
                  }
              }
          }
        })
        console.log(car)
          dispatch(seriesList(car))
      }
    })
  }
}
// 获取车型
export function getStyleList(params,hkscode){
  return (dispatch,getState)=>{
    getStyles(params).then(res=>{
      if(res.status==200&&res.data.errcode==0){
        console.log(res.data)
        let dataList=res.data.vehiclesubmodelList
        let car = [];
        dataList.forEach((item, index) => {
            if (index == 0) {
                car.push({
                    yearname: item.yearname,
                    model: [item]
                });
            } else {
                let myindex = car.findIndex(val => {
                    return val.yearname == item.yearname;
                });
                //在shoppingCartList没有对应的车牌
                if (myindex == -1) {
                    car.push({
                        yearname: item.yearname,
                        model: [item]
                    });
                } else {
                    //在shoppingCartLis找到对应的车牌
                    car[myindex].model.push(item);
                }
            }
        });
        car.sort((a, b) => {
            return Number(b.yearname)-Number(a.yearname);
        });
        car.forEach(item => {
            if (item.model && item.model.length) {
                item.model.sort((a, b) => {
                    if (a.salesname == b.salesname) {
                        return a.productionyear - b.productionyear;
                    } else {
                        return a.salesname - b.salesname;
                    }
                });
            }
        });
        car.forEach(item => {
            if (item.model && item.model.length) {
                item.sort = {};
                //第一步 按照salesname归类
                item.model.forEach(val => {
                    if (!item.sort[val.salesname]) {
                        item.sort[val.salesname] = [val];
                    } else {
                        item.sort[val.salesname].push(val);
                    }
                });
                //第二步 根据productionyear去重
                Object.keys(item.sort).forEach(key => {
                    var arr = [];
                    for (var i = 0; i < item.sort[key].length; i++) {
                        for (
                            var j = i + 1;
                            j < item.sort[key].length;
                            j++
                        ) {
                            if (
                                item.sort[key][i].productionyear ==
                                item.sort[key][j].productionyear
                            ) {
                                j = ++i;
                            }
                        }
                        arr.push( item.sort[key][i])
                    }
                    item.sort[key] = arr;
                });

                //第三步 对salesname归类后的数组按照productionyear排序
                Object.keys(item.sort).forEach(key => {
                    item.sort[key].sort((a, b) => {
                        return b.productionyear - a.productionyear;
                    });
                });
                let arr = [];
                Object.keys(item.sort).forEach(key => {
                    item.sort[key].forEach(newcar => {
                        arr.push(newcar);
                    });
                });
                item.model = arr;
            }
        });
        dispatch(styleList(car))
        if(hkscode){   //重新询价时的车型渲染
            let carInfo=[]
            car.forEach(item=>{
                item.model.forEach(car=>{
                    if(car.hkscode==hkscode){
                        carInfo.push(car)
                    }
                })  
            })
            if(carInfo.length){
                dispatch(getCarInfo(carInfo[0]))
            }
        }
      }
    })
  }
}
// vin码查车型
export function getVinCarInfo(params,hkscode){
    return (dispatch,getState)=>{
        getHkscode(params).then(res=>{
            if(res.status==200&&res.data.errcode==0){
                let cars=res.data.hksdata.rows
                let list=[]
                cars.forEach(item=>{
                    list.push(
                        {
                            brand:item.brand,
                            sonBrand:item.modelbrandmfr,//子品牌
                            carVehicle:item.familyname,
                            outputVolume:item.engineDesc,
                            producingArea:item.makenation,//item.productivetype,//产地
                            producingYear:item.productyear,
                            bodyType:item.vehicletype,  //车身类型
                            gearboxType:item.gearboxName,
                            drivingForm:item.drivetype,
                            engineModel:item.engineStyle,
                            chassisNumber:item.chassisnumber,
                            yearname:item.yearpattern,
                            salesname:item.vehiclesale,
                            carUrl:item.modelbrandlogourl,
                            hkscode:item.hkscode,
                            vin:item.vin,
                            modelbrandid:item.modelbrandid, //"品牌id",
                            makeid:item.makeid,//厂家编号
                            makename:item.brandname,//厂家名称
                            classify:item.familyname, //车系名称
                            classifyid:item.modelid, //车系id
                            cartype:item.vehiclesale //车型名称
                        }
                    )
                })
                if(hkscode){
                    list=list.filter(item=>{
                        return item.hkscode==hkscode
                    })
                }
                if(list.length){
                    if(list.length==1){
                        dispatch(getCarInfo(list[0]))
                    }else{
                        dispatch(vinCarList(list))
                    }
                }else{
                    dispatch(getCarInfo([]))
                }
                
            }
        })
    }
}
//获取某一个vin车型信息
export function setVinCarInfo(params){
    return(dispatch,getState)=>{
        let carInfo=getState().Inquiry_reducer.vinCars[params]
        dispatch(getCarInfo(carInfo))
    }
}
