import _ from "lodash"
import {
  INQUERY_DETAIL,
  ACTION_PARTS
} from "./actionTypes.js"
const initState={
  offerInfo:{},
  offerList:[], //存放右侧列表数据源
  showOffer:[], //展示右侧列表数据
}
export default function Offer_reducer(state=initState, action){
  switch(action.type){
    case INQUERY_DETAIL:
      return Object.assign(
        {},
        {offerInfo:_.cloneDeep(action.payload.InquiryInfo)},
        {offerList:_.cloneDeep(action.payload.QuotationInfo)},
        {showOffer:_.cloneDeep(action.payload.QuotationInfo)}
        )
      break
    case ACTION_PARTS:
      return Object.assign(
        {},
        state,
        {showOffer:_.cloneDeep(action.payload)}
      )
      break
    default: 
      return state
  }
}