import React, { Component } from 'react';
import {withRouter} from "react-router-dom"
import OutHeader from "comp/outHeader/index.js"
import InsideHeader from "comp/insideHeader/index.js"
import { Cookie } from "util/util.js";
class ValidateEntry extends Component{
  componentWillMount(){
    // this.props.history.push('/home')
  }
  render(){
    const {location}=this.props
    let isLogin=true;
    const flag = Cookie.getCookie("__hksuserinfo__")
    console.log('location.pathname',location.pathname)
    if(location.pathname=='/'&&!flag){
        isLogin = false
    }
    if(location.pathname === '/login' || location.pathname.indexOf('/register')!=-1 ){
      isLogin=false
    }
    return(
      <React.Fragment>
        {isLogin ? (<InsideHeader></InsideHeader>):(<OutHeader></OutHeader>)}
      </React.Fragment>
    ) 
  }

}

export default withRouter(ValidateEntry)