import { handleActions } from "redux-actions";
import * as TYPES from "./actionTypes";
//处理地址列表
 const order = handleActions(
    {
        [TYPES.ADDRESS_LIST](state, action) {
            //获取地址列表
            const address = action.payload;
            return {...state, addressList:address};
        },
        [TYPES.ADD_ADDRESS](state, action) {
            //增加地址
            const address = action.payload;
            return {...state, addressList:[...state.addressList,address]};
        },
        [TYPES.UPDATE_ADDRESS](state, action) {
            //更新地址
            const { index, address } = action.payload;
           const addressList= state.addressList.map((val, i) => (i == index ? address : val));
            return {...state,addressList}
        },
        [TYPES.DELETE_ADDRESS](state, action) {
            //删除处地址
            const index = action.payload;
            state.addressList.splice(index,1);
            return {...state,addressList:[...state.addressList]};
        },
        [TYPES.SET_GOODS_LIST](state, action) {
            //设置商品列表
            const {goodsList,total} = action.payload;
            return {...state,goodsList,total};
        },
    },
    {
        addressList:[],
        goodsList:[
            // {supplie:'',partItems:[]}
        ],
        total:0,//应付总额
    }
);



export default order 
