import React, { Component } from "react";
import { connect } from "react-redux";
import { DatePicker, Pagination } from "antd";
import moment from 'moment'
import "./index.scss";
import OrderItem from "./components/orderItem";
import { getOrderList, getPageSize, setPageSize } from "../actions";
@connect(
    state => ({
        // orderListOfState: state.OrderManage_reducer.orderListOfState,
        orderList: state.OrderManage_reducer.orderList,
        userinfo: state.Login_reducer.hks_userInfo,
        totalSize: state.OrderManage_reducer.totalSize
    }),
    { getOrderList, getPageSize, setPageSize }
)
class OrderManage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            now: 0, //tab 索引
            partName: "", //配件名称
            inquiryNumber: "", //询价单号
            startTime: "", //下单时间
            endTime: "", //下单时间
            pageStart: 1, //sql查询间隔的页数
            pageSize: 10, //每页多少条
            current: 1 //当前页
        };
        // tab栏
        this.tabs = [
            { name: "所有订单" },
            { name: "待付款" },
            { name: "待发货" },
            { name: "待收货" },
            { name: "已完成" },
            { name: "已取消" },
            { name: "退货退款" }
        ];
    }
    componentWillMount() {
        this.search(true);
    }
    componentDidMount() {
        /*
        const orderList = [
            {
                order: {
                    createtime: "2019-01-11 12:00:32",
                    orderNum: "Q2019011100546",
                    supplieName: "好快省",
                    note: "测试",
                    realAmount: 600
                },
                partItems: [
                    {
                        id: 1,
                        partName: "机油滤清器",
                        oe: 16127233840,
                        price: 150,
                        num: 1,
                        state: 1
                    },
                    {
                        id: 2,
                        partName: "机油滤清器",
                        oe: 16127233840,
                        price: 150,
                        num: 1,
                        state: 1
                    },
                    {
                        id: 3,
                        partName: "机油滤清器",
                        oe: 16127233840,
                        price: 150,
                        num: 1,
                        state: 1
                    },
                    {
                        id: 4,
                        partName: "机油滤清器",
                        oe: 16127233840,
                        price: 150,
                        num: 1,
                        state: 1
                    }
                ]
            },
            {
                order: {
                    createtime: "2019-01-11 12:00:31",
                    orderNum: "Q2019011100546",
                    supplieName: "好快省",
                    note: "测试",
                    realAmount: 300
                },
                partItems: [
                    {
                        id: 1,
                        partName: "机油滤清器",
                        oe: 16127233840,
                        price: 150,
                        num: 1,
                        state: 1
                    },
                    {
                        id: 2,
                        partName: "机油滤清器",
                        oe: 16127233840,
                        price: 150,
                        num: 1,
                        state: 1
                    },
                    {
                        id: 3,
                        partName: "机油滤清器",
                        oe: 16127233840,
                        price: 150,
                        num: 1,
                        state: 1
                    }
                ]
            }
        ];
        this.props.getOrderList(orderList)
        */
        const {
            startTime,
            endTime,
            partName,
            inquiryNumber,
            pageStart,
            pageSize
        } = this.state;
        const { userinfo, getPageSize } = this.props;
        const orderData = {
            purchaseOrder: {
                accountName: userinfo.customeraccount.accountName,
                starttime: startTime || null,
                endtime: endTime || null
            },
            partToPurchase: {
                partName,
                inquiryNumber
            },
            pageStart,
            pageSize
        };
        getPageSize(orderData);
    }
    handleChange = e => {
        const value = e.target.value.trim();
        const name = e.target.name;
        this.setState({ [name]: value });
    };
    handleChangeEndTime = (moment, dateS) => {
        this.setState(
            {
                endTime: dateS
            }
        );
    };
    handleChangeStartTime = (moment, dateS) => {
        this.setState({
            startTime: dateS
        });
    };
    switchTab = now => {
        const {
            startTime,
            endTime,
            partName,
            inquiryNumber,
            pageSize
        } = this.state;
        const { userinfo, getOrderList, getPageSize } = this.props;
        const orderData = {
            purchaseOrder: {
                accountName: userinfo.customeraccount.accountName,
                starttime: startTime || null,
                endtime: endTime || null
            },
            partToPurchase: {
                partName,
                inquiryNumber
            },
            pageStart: 0,
            pageSize
        };
        let returnGoodsData = {};
        //重置到第一页
        this.setState({
            now,
            current: 1
        });
        switch (now) {
            //待付款
            case 1:
                orderData.purchaseOrder.paymentState = 0; //未付款
                orderData.purchaseOrder.purchaseOrderState = 1;

                break;
            //待发货
            case 2:
                orderData.purchaseOrder.paymentState = 1; //已付款
                orderData.purchaseOrder.consigneeState = 3; //待发货
                orderData.purchaseOrder.purchaseOrderState = 1;
                break;
            //待收货
            case 3:
                orderData.purchaseOrder.paymentState = 1; //已付款
                orderData.purchaseOrder.consigneeState = 0; //待收货
                orderData.purchaseOrder.purchaseOrderState = 1;
                break;
            //已完成
            case 4:
                orderData.purchaseOrder.paymentState = 1; //已付款
                orderData.purchaseOrder.serviceState = 2; //已完成
                orderData.purchaseOrder.purchaseOrderState = 1;
                break;
            //已取消
            case 5:
                orderData.purchaseOrder.purchaseOrderState = 0; //订单失效
                break;
            //退货退款
            case 6:
                returnGoodsData = {
                    customerID: userinfo.customer.customerID,
                    startDate: startTime || null,
                    endDate: endTime || null,
                    isQueryDetail: 1
                }; //订单失效
                break;

            default:
                break;
        }
        if (now == 6) {
            //查询退货退款
            getOrderList(null, returnGoodsData);
        } else {
            //查询列表数据
            getOrderList(orderData);
            //查询列表总数
            getPageSize(orderData);
        }
    };
    //改变pagesize触发
    onShowSizeChange = (current, pageSize) => {
        const { now } = this.state;
        // 如果是退货退款就要做前端分页
        this.setState(
            {
                pageStart: 1,
                pageSize: pageSize || this.state.pageSize,
                current: 1
            },
            () => {
                if (now == 6) {
                    //退货退款前端分页
                } else {
                    this.search();
                }
            }
        );
    };
    //current触发
    jumpChange = pageNumber => {
        // 如果是退货退款就要做前端分页

        const { now } = this.state;
        this.setState(
            {
                pageStart: pageNumber,
                current: pageNumber
            },
            () => {
                if (now == 6) {
                    //前端分页
                } else {
                    this.search();
                }
            }
        );
    };
    //查询订单 reset是否重置,从第一页开始查
    search = reset => {
        const {
            startTime,
            endTime,
            partName,
            inquiryNumber,
            pageStart,
            pageSize
        } = this.state;
        const { userinfo, getOrderList ,getPageSize} = this.props;
        let pageStartNew;
        if (reset) {
            pageStartNew = 0;
        } else {
            pageStartNew = (pageStart - 1) * pageSize;
        }
        const orderData = {
            purchaseOrder: {
                accountName: userinfo.customeraccount.accountName,
                starttime: startTime || null,
                endtime: endTime || null
            },
            partToPurchase: {
                partName,
                inquiryNumber
            },
            pageStart: pageStartNew,
            pageSize
        };
        const returnGoodsData = {
            customerID: userinfo.customer.customerID,
            startDate: startTime || null,
            endDate: endTime || null,
            isQueryDetail: 1
        };
        // returnGoodsData //暂时不查退货退款的数据
        getOrderList(orderData);
        getPageSize(orderData);
    };
    classifyByState = () => {};
    //确认收货的回调
    confirmCallback=()=>{
        this.search()
    }
    render() {
        const { now, partName, inquiryNumber, current } = this.state;
        // orderListOfState
        const { orderList, totalSize } = this.props;
        let orderListNew;
        //退货退款 做成前端分页
        if (now == 6) {
            const { current, pageSize } = this.state;
            const { setPageSize } = this.props;
            const start = (current - 1) * pageSize;
            const end = current * pageSize;
            orderListNew = orderList.slice(start, end);
            setPageSize(orderList.length);
        } else {
            orderListNew = orderList;
        }
        const tabItems = this.tabs.map((v, i) => (
            <span
                key={v.name}
                className={now == i ? `option active` : `option`}
                onClick={() => {
                    this.switchTab(i);
                }}
            >
                {v.name}{" "}
                {/* {orderListOfState[i] ? (
                    <span className="warn">{orderListOfState[i].length}</span>
                ) : null} */}
            </span>
        ));

        const orderItems = orderListNew.map(v => (
            <OrderItem
                orderInfo={v}
                confirmCallback={this.confirmCallback}
                key={
                    v.purchaseorder.purchaseOrderNumber ||
                    v.purchaseorder.applyTime
                }
            />
        ));
        // const orderItems =
        //     orderListOfState &&
        //     orderListOfState[now] &&
        //     orderListOfState[now].map(v => (
        //         <OrderItem
        //             orderInfo={v}
        //             key={
        //                 v.purchaseorder.purchaseOrderNumber ||
        //                 v.purchaseorder.applyTime
        //             }
        //         />
        //     ));

        return (
            <div className="orderManage container">
                <div className="order-tab">{tabItems}</div>

                <div className="search">
                    <label>
                        配件名称:
                        <input
                            className="box-hover"
                            type="text"
                            name="partName"
                            value={partName}
                            onChange={this.handleChange}
                        />
                    </label>
                    <label>
                        询价单号：
                        <input
                            className="box-hover"
                            type="text"
                            name="inquiryNumber"
                            value={inquiryNumber}
                            onChange={this.handleChange}
                        />
                    </label>
                    <label>下单时间</label>
                    
                    <DatePicker onChange={this.handleChangeStartTime} defaultPickerValue={moment(new Date(), 'YYYY-MM-DD')} />
                    -
                    <DatePicker
                        className="endTime"
                        onChange={this.handleChangeEndTime}
                        defaultPickerValue={moment(new Date(), 'YYYY-MM-DD')}
                    />
                    <button
                        onClick={() => {
                            this.search(true);
                        }}
                    >
                        搜索
                    </button>
                </div>

                <div className="order-con">
                    <div className="con-header">
                        <span className="goodInfo">商品信息</span>
                        <span className="price">单价</span>
                        <span className="count">数量</span>
                        <span className="realAmount">实付款（元）</span>
                        <span className="state">交易状态</span>
                        <span className="operation">交易操作</span>
                    </div>
                    {orderItems}
                </div>
                <div className="order-page">
                    <Pagination
                        current={current}
                        showSizeChanger
                        onShowSizeChange={this.onShowSizeChange}
                        // defaultCurrent={1}
                        total={totalSize}
                        showQuickJumper
                        onChange={this.jumpChange}
                    />
                </div>
            </div>
        );
    }
}

export default OrderManage;
