import React, { Component } from "react";
import PropTypes from "prop-types";
import { Icon } from "antd";
// 验证滑动验证
class Tips extends Component {
    static defaultProps = {
        // errMsg: "该手机号已注册"
    };
    render() {
        const { error, errMsg } = this.props;
        let tip;
        // console.log(error);
        switch (error) {
            case true:
                tip = (
                    <span>
                        <Icon
                            type="exclamation-circle"
                            theme="filled"
                            style={{ color: "#f33" }}
                            className="mr8"
                        />

                        <span>{errMsg}</span>
                    </span>
                );
                break;
            case false:
                tip = (
                    <Icon
                        type="check-circle"
                        style={{ color: "#4dc43d" }}
                        theme="filled"
                    />
                );
                break;
            default:
                tip = null;
                break;
        }
        // const tip = error ===? (
        //     <span>
        //         <Icon
        //             type="exclamation-circle"
        //             theme="filled"
        //             style={{ color: "#f33" }}
        //             className="mr8"
        //         />

        //         <span>{errMsg}</span>
        //     </span>
        // ) : (
        //     <Icon
        //         type="check-circle"
        //         style={{ color: "#4dc43d" }}
        //         theme="filled"
        //     />
        // );
        return tip;
    }
}
Tips.propTypes = {
    error: PropTypes.oneOfType([PropTypes.string,PropTypes.bool]),
    errMsg: PropTypes.string
};
export default Tips;
