import axios from 'axios'
//获取购物车
export const getShopList = (data) => {
    return axios({
        url: 'auth/shoppingcart/queryWithInquiryNumber',
        method: 'post',
        data
    }).then(res => res, err => err)
}
//删除购物车
export const delShopList = (data) => {
    return axios({
        url: 'auth/shoppingcart/delete',
        method: 'post',
        data
    }).then(res => res, err => err)
}
//更新购物车
export const updateShopList = (data) => {
    return axios({
        url: 'auth/shoppingcart/update',
        method: 'post',
        data
    }).then(res => res, err => err)
}