/**
 * 从车牌到车系的接口
 */
import { axios } from '@/config/api'

//获取汽车品牌,
export const getBrands = (data={}) => {
    return axios({
        url: 'auth/vehicleinfo/getmodelbrand',
        method: 'post',
        data
    }).then(res => res, err => err)
}
//获取车系,
export const getSeries = (data) => {
    return axios({
        url: 'auth/vehicleinfo/getvehiclemodel',
        method: 'post',
        data
    }).then(res => res, err => err)
}
//获取车款,
export const getStyles = (data) => {
    return axios({
        url: 'auth/vehicleinfo/getvehiclesubmodel',
        method: 'post',
        data
    }).then(res => res, err => err)
}
//根据VIN查询车辆信息
export const getHkscode = (data) => {
    return axios({
        url: 'auth/epcs/vehicle/getHkscode',
        method: 'post',
        data,
        loading:true
    }).then(res => res, err => err)
}
//配件标准名称查询
export const queryPart = (data) => {
    return axios({
        url: 'auth/epcs/partTerminology/getStandardName',
        method: 'post',
        data
    }).then(res => res.data, err => err)
}
//生成询价单
export const saveOrder = (data={}) => {
    return axios({
        url: '/Appdata/InquiryAddOrUpdate.aspx',
        method: 'post',
        thirdInterface:true,
        data
    }).then(res => res, err => err)
}
//重新询价
export const queryAgain = (data={}) => {
    return axios({
        url: '/Appdata/Reinquiry.aspx',
        method: 'post',
        thirdInterface:true,
        data
    }).then(res => res, err => err)
}