import axios from "axios";
//获取省市区接口
export const getArea = data => {
    return axios({
        url: "auth/areaPrice/query",
        method: "post",
        data
    }).then(res => res, err => err);
};

//查询库存
export const getInventory = data => {
    return axios({
        url: "auth/datastore/queryInventory",
        method: "post",
        data
    }).then(res => res, err => err);
};

//订单生成
export const orderGenerate = data => {
    return axios({
        url: "auth/order/generateNewHks",
        method: "post",
        data
    }).then(res => res, err => err);
};
