//对与是否登录及注册的判断组件
import ValidateEntry from 'comp/validateEntry';
import ValidateFooter from 'comp/validateFooter';
import { PersistGate } from 'redux-persist/integration/react'
import ScrollToTop  from 'comp/withrouter'
import AuthRouter  from 'comp/AuthRouter'
import lazyComponent  from 'comp/lazyComponent'
import { Cookie } from "util/util.js";
const Login = lazyComponent(()=>(import ('cont/login')))
const Register = lazyComponent(()=>(import ('cont/register')))
const Home = lazyComponent(()=>(import ('cont/home')))
const FloorDetail = lazyComponent(()=>(import ('cont/floorDetail')))
const Inquiry = lazyComponent(()=>(import ('cont/inquiry')))
const Offer = lazyComponent(()=>(import ('cont/offer')))
const OfferDetail = lazyComponent(()=>(import ('cont/offerDetail')))
const Shop = lazyComponent(()=>(import ('cont/shop')))
const Order = lazyComponent(()=>(import ('cont/order')))
const Maintain = lazyComponent(()=>(import ('cont/maintain')))
const Cashier = lazyComponent(()=>(import ('cont/cashier')))
const OrderManage = lazyComponent(()=>(import ('cont/orderManage')))
const PaySuccess= lazyComponent(()=>(import ('cont/paySuccess')))
const OrderDetail = lazyComponent(()=>(import ('cont/orderDetail')))
const UserProtocol = lazyComponent(()=>(import ('cont/userProtocol')))
const RegisterSuccess = lazyComponent(()=>(import ('cont/registerSuccess')))

const Root = ({ store,persistor }) => (
  <Provider store={store}>
    <PersistGate loading={null} persistor={persistor}>  
    <HashRouter>
      <ScrollToTop >
        {/* 没有登录就拦截,跳到登录页 */}
        <AuthRouter></AuthRouter> 
        <ValidateEntry></ValidateEntry>
        <Switch>
          <Route exact path="/" component={Cookie.getCookie("__hksuserinfo__")?Home:Login} />
          <Route exact path="/login" component={Login} />
          <Route exact path="/register/:id" component={Register} />
          <Route exact path="/home" component={Home} />
          <Route exact path="/homeDetail" component={FloorDetail} />
          <Route exact path="/inquiry" component={Inquiry} />
          <Route exact path="/inquiry/:id" component={Inquiry} />
          <Route exact path="/offer" component={Offer} />
          <Route exact path="/offerDetail/:id" component={OfferDetail} />
          <Route exact path="/shop" component={Shop} />
          <Route exact path="/order" component={Order} />
          <Route exact path="/maintain" component={Maintain} />
          <Route exact path="/cashier/:order" component={Cashier} />
          <Route exact path="/orderManage" component={OrderManage} />
          <Route exact path="/paySuccess" component={PaySuccess} />
          <Route exact path="/orderDetail/:id" component={OrderDetail} />
          <Route exact path="/register/:id" component={Register} />
          <Route exact path="/userProtocol" component={UserProtocol} />
          <Route exact path="/registerSuccess" component={RegisterSuccess} />
          <Redirect from="/register" to="/register/1"></Redirect>
          <Redirect from="/inquiry" to="/inquiry/1"></Redirect>
          <Redirect from="/cashier" to="/cashier/1"></Redirect>
        </Switch>

        {/* <InsideFooter></InsideFooter> */}
        <ValidateFooter></ValidateFooter>
        {/* <DevTools /> */}
      </ScrollToTop >

    </HashRouter>
    </PersistGate>
  </Provider>
)

Root.propTypes = {
  store: PropTypes.object.isRequired,
}
export default Root
