import React, { Component } from "react"
import {withRouter} from "react-router-dom"
import { Input, Button } from "antd"
import Tips from "./Tips"
// 验证手机
@withRouter
class ValiCompany extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isRegister: null,
        };
    }
  
    toHome=()=>{
        this.props.history.push('/home')
    }
    render() {
        const { isRegister } = this.state;
        const error = isRegister !== null && isRegister === true;
        console.log(error);
        return (
            <div className="vali valiCompany">
              
                <div>
                    <span className="label-input">
                        <i className="mr5">*</i>公司名称
                    </span>
                    <Input className="mr15" />
                    <Tips error={error} />
                </div>
                <div>
                    <span className="label-input">
                        <i className="mr5">*</i>联系人姓名
                    </span>
                    <Input className="mr15" />
                    <Tips error={error} />
                </div>
                <div>
                    <span className="label-input">
                        <i className="mr5">*</i>社会统一信用代码
                    </span>
                    <Input className="mr15" />
                    <Tips error={error} />
                </div>
                <div>
                    <span className="label-input">
                        <i className="mr5">*</i>公司所在地
                    </span>
                    <Input className="mr15" />
                    <Tips error={error} />
                </div>
                <div>
                    <span className="label-input">
                        <i className="mr5">*</i>详细地址
                    </span>
                    <Input className="mr15" />
                    <Tips error={error} />
                </div>
               
                <div className="valiphone-item">
                    <Button className="complete" onClick={this.toHome}>完成注册</Button>
                </div>
            </div>
        );
    }
}

export default ValiCompany;
