import { createAction } from "redux-actions";
import * as TYPES from "./actionTypes";
import * as ORDER from "./api.js";
import { orderUpdate } from "cont/order/api";

export const orderList = createAction(TYPES.ORDER_LIST); //订单列表
export const setPageSize = createAction(TYPES.PAGE_SIZE); //订单列表
export const getOrderList = (orderData, returnGoodsData) => dispatch => {
    //为了对应页面的显示索引,和文档中的状态不一致
    let orderListOfState = {
        0: [], //全部订单
        1: [], //待支付
        2: [], //待发货
        3: [], //待收货
        4: [], //已完成
        5: [], //已取消
        6: [] //退货退款
    };
    orderData &&
        ORDER.getOrder(orderData).then(res => {
            if (res.data.errcode == 0) {
                const purchaseorders = res.data.purchaseorders;

                //按照状态进行分类
                // purchaseorders.forEach(val => {
                //     let state = val.purchaseorder.state;
                //     switch (state) {
                //         case 1:
                //             orderListOfState[state].push(val);
                //             break;
                //         case 2:
                //         case 3:
                //             orderListOfState[2].push(val);
                //             break;
                //         case 4:
                //             orderListOfState[3].push(val);
                //             break;
                //         case 5:
                //             orderListOfState[4].push(val);
                //             break;
                //         case 6:
                //             orderListOfState[5].push(val);
                //             // orderListOfState[6].push(val)
                //             break;
                //         default:
                //             break;
                //     }
                // });
                // orderListOfState[0] = purchaseorders;
                dispatch(orderList(purchaseorders));
            }else{
                dispatch(orderList([])); 
            }
        });
    //查询退货退款订单
    returnGoodsData &&
        ORDER.getReturnGoods(returnGoodsData).then(res => {
            if (res.data.errcode == 0) {
                const returnGoodsList = res.data.returnGoodsList; //退货退款单主体
                const returnGoodsDetailList = res.data.returnGoodsDetailList; //退货退款配件详情
                const order = [];
                returnGoodsList.forEach(returnOrder => {
                    const parttopurchases = [
                        returnGoodsDetailList.find(
                            v =>
                                v.returnGoodsNumber ==
                                returnOrder.returnGoodsNumber
                        )
                    ];

                    const returnObj = {
                        purchaseorder: returnOrder, //退货
                        parttopurchases //退货商品详情
                    };
                    order.push(returnObj);
                    // orderListOfState[6].push(returnObj);
                    // orderListOfState[0].push(returnObj);

                    // orderListOfState = Object.assign({}, orderListOfState);

                    // dispatch(orderList(orderListOfState));
                });
                dispatch(orderList(order));
            }
        });
};
//获取总条数
export const getPageSize = orderData => dispatch => {
    return ORDER.getOrderPage(orderData).then(res => {
        if (res.data.errcode == 0) {
            const totalSize = res.data.totalSize;
            dispatch(setPageSize(totalSize));
        }else{
            dispatch(setPageSize(0)) 
        }
    });
};

