import React, { Component } from "react";
import { Checkbox, message, Modal } from "antd";
import { connect } from "react-redux";
import {
    getShopList,
    delShopGoods,
    updateShopGoods,
    changeFirstCheck,
    changeSecondCheck,
    changeAllCheck,
    changeNum,
    getCountAndPrice
} from "../actions";
import { setShopList } from "cont/order/actions";
import InquiryItem from "./components/InquiryItem";
import "./index.scss";
import { debounce } from "util/util.js";
@connect(
    state => ({
        ...state.Shop_reducer,
        userInfo: state.Login_reducer.hks_userInfo
    }),
    {
        getShopList,
        delShopGoods,
        updateShopGoods,
        changeFirstCheck,
        changeSecondCheck,
        changeAllCheck,
        changeNum,
        getCountAndPrice,
        setShopList
    }
)
class ShoppingCart extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isFixed: false //是否固定显示
        };
        this.shopRef = React.createRef();
        const { userInfo } = this.props;
        const cityId = userInfo.customer.areaID.toString().slice(0,4)
        this.queryData = {
            shoppingCart: {
                customerAccountID: userInfo.customeraccount.customerAccountID
                // "inquiryNumber":"XJ190121000005"
            },
            city: userInfo.customer.cityID||cityId,
            isAsc: false
        };
    }
    componentWillMount() {
        const { checkAll } = this.props;
        //获取新的购物车数据
        this.props.getShopList(this.queryData, checkAll);
    }
    componentDidMount() {
        this.documentHeight = document.documentElement.clientHeight; //屏幕区域的高度
        this.headerHeight = 90; //汽配商城整体头部的高度
        const bindHandScroll = debounce(this.handleScroll, 100);
        document.onscroll = bindHandScroll;
    }
    handleScroll = () => {
        if (this.shopRef.current) {
            let shopHeight = this.shopRef.current.clientHeight; //购物车的高度
            let scrollTop = document.documentElement.scrollTop; //滚动的高度
            let documentHeight = this.documentHeight;
            let headerHeight = this.headerHeight;
            //滚动高度+屏幕高度 小于 购物车的高度+头部高度 此时应该固定
            if (
                scrollTop + documentHeight < shopHeight + headerHeight &&
                !this.state.isFixed
            ) {
                this.setState({
                    isFixed: true
                });
            } else if (
                scrollTop + documentHeight > shopHeight + headerHeight &&
                this.state.isFixed
            ) {
                this.setState({
                    isFixed: false
                });
            }
        }
    };

    checkAllChange = e => {
        const checked = e.target.checked;
        this.props.changeAllCheck(checked);
        this.props.getCountAndPrice();
    };
    //处理二级的选中状态
    handleChangeForInquiry = (index, checked) => {
        this.props.changeFirstCheck({ index, checked });
        this.props.getCountAndPrice();
    };
    //处理三级选中
    handleChangeForPart = (firstIndex, secondIndex, checked) => {
        this.props.changeSecondCheck({ firstIndex, secondIndex, checked });
        this.props.getCountAndPrice();
    };
    //数量处理
    handChangeNum = (firstIndex, secondIndex, num) => {
        this.props.changeNum({ firstIndex, secondIndex, num });
        this.props.getCountAndPrice();
    };
    //结算 设置提交订单的redux数据
    toSubmitOrder = () => {
        const { checkedGoods } = this.props;
        const goodsList = [];
        let total = 0;
        checkedGoods.forEach((item, index) => {
            total += item.partItem.price * item.partItem.quantity;
            //TODO:根据供应商拆分
            if (index == 0) {
                goodsList.push({
                    supplie: item.partItem.supplieID,
                    supplieName: item.partItem.supplieName,
                    partItems: [item.partItem]
                });
            } else {
                let myindex = goodsList.findIndex(val => {
                    return val.supplie == item.partItem.supplieID;
                });
                //在shoppingCartList没有对应的车牌
                if (myindex == -1) {
                    goodsList.push({
                        supplie: item.partItem.supplieID,
                        supplieName: item.partItem.supplieName,
                        partItems: [item.partItem]
                    });
                } else {
                    //在shoppingCartLis找到对应的车牌
                    goodsList[myindex].partItems.push(item.partItem);
                }
            }
        });
        this.props.setShopList({ goodsList, total });
        if (!goodsList.length) {
            message.info("请选择要购买的商品！");
            return;
        }
        this.props.history.push("/order");

        // console.log(checkedGoods)
    };
    //删除选定的商品
    delCheckedGoods = () => {
        const confirm = Modal.confirm;
        const { checkedGoods } = this.props;
        const me = this;
        if (!checkedGoods.length) {
            message.info("请选择要删除的商品");
            return;
        }
        confirm({
            title: "确定删除已选中的商品？",
            okType: "danger",
            onOk() {
                const {
                    checkedGoods,
                    delShopGoods,
                    checkAll,
                    userInfo
                } = me.props;

                const queryData = {
                    customerAccountID:
                        userInfo.customeraccount.customerAccountID,
                    shoppingCartList: []
                };
                queryData.shoppingCartList = checkedGoods.map(v => ({
                    partItemID: v.partItem.partItemID,
                    licenseNo: v.vehicle && v.vehicle.licenseNo,
                    quoteOrderID: v.partItem.quoteOrderID
                }));
                delShopGoods(queryData, me.queryData, checkAll);
            }
        });
    };
    //删除已失效商品
    delInvaliddGoods = () => {
        const confirm = Modal.confirm;
        // const { checkedGoods } = this.props;
        const me = this;
        confirm({
            title: "确定删除已选中的商品？",
            okType: "danger",
            onOk() {
                //根据isValid属性去判断
                const { delShopGoods, checkAll, userInfo, shopList } = me.props;

                const delData = {
                    customerAccountID:
                        userInfo.customeraccount.customerAccountID,
                    shoppingCartList: []
                };
                shopList &&
                    shopList.forEach(val => {
                        if (val.partItems) {
                            val.partItems.forEach(part => {
                                if (!part.isValid) {
                                    const delObj = {
                                        partItemID: part.partItemID,
                                        licenseNo:
                                            part.vehicle &&
                                            part.vehicle.licenseNo,
                                        quoteOrderID: part.quoteOrderID
                                    };
                                    delData.shoppingCartList.push(delObj);
                                }
                            });
                        }
                    });
                delShopGoods(delData, me.queryData, checkAll);
            }
        });
    };
    render() {
        const { isFixed } = this.state;
        const {
            shopList,
            checkAll,
            totalPrice,
            checkedCount,
            delShopGoods,
            updateShopGoods,
            userInfo
        } = this.props;
        const inquiryItems = shopList.map((val, index) => {
            return (
                <InquiryItem
                    customerAccountID={
                        userInfo.customeraccount.customerAccountID
                    }
                    firstIndex={index}
                    inquiryInfo={val}
                    key={index}
                    handleChangeForInquiry={this.handleChangeForInquiry}
                    handleChangeForPart={this.handleChangeForPart}
                    handChangeNum={this.handChangeNum}
                    delShopGoods={delShopGoods}
                    updateShopGoods={updateShopGoods}
                    queryData={this.queryData}
                    checkAll={checkAll}
                />
            );
        });
        const footer = isFixed ? (
            <div className="footer-fixed">
                <div className="footer">
                    <span className="check dib">
                        <Checkbox
                            className="mr8"
                            onChange={this.checkAllChange}
                            checked={checkAll}
                        />
                        全选
                    </span>
                    <span className="selected" onClick={this.delCheckedGoods}>
                        删除选中的商品
                    </span>
                    <span className="invalid" onClick={this.delInvaliddGoods}>
                        删除失效的商品
                    </span>
                    <span className="selected-count">
                        您已选中<i className="warn"> {checkedCount} </i>件商品
                    </span>
                    <span className="total">
                        总价：
                        <strong className="warn">{totalPrice}</strong>
                    </span>
                    <button
                        className="fr settlement"
                        onClick={this.toSubmitOrder}
                    >
                        结算
                    </button>
                </div>
            </div>
        ) : (
            <div className="footer">
                <span className="check dib">
                    <Checkbox
                        className="mr8"
                        checked={checkAll}
                        onChange={this.checkAllChange}
                    />
                    全选
                </span>
                <span className="selected" onClick={this.delCheckedGoods}>
                    删除选中的商品
                </span>
                <span className="invalid" onClick={this.delInvaliddGoods}>
                    删除失效的商品
                </span>
                <span className="selected-count">
                    您已选中<i className="warn"> {checkedCount} </i>件商品
                </span>
                <span className="total">
                    总价：
                    <strong className="warn">{totalPrice}</strong>
                </span>
                <button className="fr settlement" onClick={this.toSubmitOrder}>
                    结算
                </button>
            </div>
        );
        return (
            <div className="shopCar container" ref={this.shopRef}>
                <div className="header">
                    <span className="check">
                        <Checkbox
                            className="mr8"
                            checked={checkAll}
                            onChange={this.checkAllChange}
                        />
                        全选
                    </span>
                    <span className="info">商品信息</span>
                    <span className="support">供应商/库存</span>
                    <span className="price">单价（元）</span>
                    <span className="count">数量</span>
                    <span className="unit">单位</span>
                    <span className="amount">金额（元）</span>
                    <span className="del">删除</span>
                </div>
                <div className="content">{inquiryItems}</div>
                {footer}
            </div>
        );
    }
}

export default ShoppingCart;
