import { handleActions } from "redux-actions";
import * as TYPES from "./actionTypes";
//处理购物车列表
export const shopList = handleActions(
    {
            //获取购物车列表 传递购物车列表
        //收集失效的
        [TYPES.SHOP_LIST](state, action) {
            const shopList = action.payload;
            return {...state, shopList};
        },
        [TYPES.CHANGE_ALL_CHECKED](state, action) {
            //全选 传递选中的值
            const checkAll = action.payload; //全选
            const shopList = state.shopList.map(val => {
                val.checked = checkAll;
                val.partItems = val.partItems.map(obj=>{
                    obj.checked = checkAll;
                    return obj
                })
                return val;
            })
            return {shopList,checkAll};
        },
        //一级选中做的修改
        [TYPES.CHANGE_FIRST_CHECKED](state, action) {
            //更新地址
            const { index, checked } = action.payload;
            const shopList = state.shopList.map(
                (val, i) => {
                    if (i == index) {
                        val.partItems = val.partItems.map((obj) => {
                            obj.checked = checked;
                            return obj;
                        });
                        val.checked = checked;
                    }
                    return val;
                }
            )
            const checkAll = shopList.every(val=>{
                return val.checked
            })
            return {checkAll,shopList};
        },
        [TYPES.CHANGE_SECOND_CHECKED](state, action) {
            //二级选中
            const { firstIndex,secondIndex, checked } = action.payload;
            const shopList = state.shopList.map((val, i) => {
                if (i == firstIndex) {
                    val.partItems = val.partItems.map((obj, j) => {
                        if (j == secondIndex) {
                            obj.checked = checked;
                        }
                        return obj;
                    });
                    //下面的三级全部选中
                    val.checked = val.partItems.every(obj => {
                        return obj.checked;
                    });
                }
                return val;
            })
            const checkAll = shopList.every(val=>{
                return val.checked
            })
            return {checkAll,shopList};
        },
        [TYPES.CHANGE_NUM](state, action) {
            //二级选中
            const { firstIndex,secondIndex, num } = action.payload;
            const shopList = state.shopList.map((val, i) => {
                if (i == firstIndex) {
                    val.partItems = val.partItems.map((obj, j) => {
                        if (j == secondIndex) {
                            obj.quantity = num;
                        }
                        return obj;
                    });
                }
                return val;
            })
            
            return {...state,shopList};
        },
        //获取选中的数据金额 收集选中的商品
        [TYPES.GET_CHECKEDCOUNT](state, action) {
           let checkedCount = 0,totalPrice=0,checkedGoods=[];
            state.shopList.forEach((val, i) => {
                val.partItems&&val.partItems.forEach(obj=>{
                    if(obj.checked){
                        checkedCount+=Number(obj.quantity)
                        totalPrice+=(obj.quantity*obj.price)
                        checkedGoods.push({
                            vehicle:val.vehicle,
                            partItem:obj
                        })
                    }
                })
            })
            return {...state,checkedCount,totalPrice,checkedGoods};
        },
        // //删除商品 
        // [TYPES.DELETE_GOOD](state, action) {
        //    let checkedCount = 0,totalPrice=0,checkedGoods=[];
        //     state.shopList.forEach((val, i) => {
        //         val.partItems&&val.partItems.forEach(obj=>{
        //             if(obj.checked){
        //                 checkedCount+=Number(obj.quantity)
        //                 totalPrice+=(obj.quantity*obj.price)
        //                 checkedGoods.push({
        //                     vehicle:val,
        //                     partItem:obj
        //                 })
        //             }
        //         })
        //     })
        //     return {...state,checkedCount,totalPrice,checkedGoods};
        // },

    },
    {
        shopList: [],//购物车列表
        checkAll: false,//全选
        checkedCount:0,//选中的数量
        totalPrice:0,//选中总价
        checkedGoods:[],//选中的商品
        invalidGoods:[],//失效商品
    }
);

export default shopList;
