import React from 'react'
import { Icon, Row, Col } from 'antd';
import './index.scss'

function OutHeader() {
  return (
    <Row>
      <Col span={16} push={4} className="out-header">
        <div className="out-left">
          <span className="logo"></span>
          <h3 className="title">汽配商城</h3>
        </div>
        <div className="out-right">
          <Icon type="phone" style={{ fontSize: '26px', color: '#fdd000', marginRight:'12px' }} />
          <span>客服热线：4000-911-900</span>
        </div>
      </Col>
    </Row>
  )
}
export default OutHeader