import React, { Component } from "react";
// import { Checkbox, Icon } from "antd";
class PartItem extends Component {
    render() {
        const {partInfo} = this.props
        return (
            <div className="partItem">
                <div className="partInfo dib">
                    <div style={{color:'#333'}}>
                        <span>{partInfo.partName} | </span>
                        <span>原厂零件号：{partInfo.oe}</span>
                    </div>
                    <div>
                        <span className="brand">品牌 {partInfo.partBrandName}</span>
                        <span className=" dib">
                            <span className="box">运费寄付</span>
                            <span className="box">24小时内到货</span>
                            <span className="box">质保三个月</span>
                        </span>
                    </div>
                </div>
                    <span className="price dib">￥{partInfo.price}</span>
                    <span className="count dib">x{partInfo.quantity}</span>
                    <span className="unit dib">{!partInfo.partSize||partInfo.partSize==1? partInfo.partSizeUOM:partInfo.partSize+partInfo.partSizeUOM}</span>
                    <span className="amount dib warn">￥{parseFloat(partInfo.price*partInfo.quantity).toFixed(2)}</span>
            </div>
        );
    }
}

export default PartItem;
