import React, { Component } from "react";
import { Button } from "antd";
import { connect } from "react-redux";
import "./index.scss";
import TimeLine from "./components/TimeLine";
import OrderItem from "./components/OrderItem.js";
import { getOrderDetail } from "../actions";
import { orderUpdate } from "cont/order/api";


@connect(
    state => ({
        orderDetail: state.OrderDetail_reducer.orderDetail,
        stateBtntextMap: state.OrderManage_reducer.stateBtntextMap
    }),
    { getOrderDetail }
)
class OrderDetail extends Component {
    constructor(props) {
        super(props);
        console.log(this.props);
        // 1-待支付，2-待确认，3-待发货，4-待收货，5-已完成，6-已取消

        this.stateMap = {
            1: "待付款",
            2: "待发货",
            3: "待发货",
            4: "待收货",
            5: "已完成",
            6: "已取消"
        };
        this.payMap = {
            1: "微信支付",
            2: "支付宝支付",
            3: "账号支付",
            4: "蓝金灵支付"
        };
        this.shippingMethodMap = {
            1: "自提",
            2: "物流",
            3: "快递"
        };
    }
    componentDidMount() {
        console.log(this.props);
      this.getData()
    }
    getData=()=>{
        const { getOrderDetail } = this.props;
        const { id } = this.props.match.params;
        const data = { purchaseOrderNumber: id };
        getOrderDetail(data); 
    }
    handleBtnClick=(state)=>{
        const purchaseOrderNumber = this.props.match.params.id;

        switch (state) {
            // 立即付款
            case 1:
                const {history} = this.props
                history.push(`/cashier/${purchaseOrderNumber}`)
                break;
            // 确认收货 TODO:
            case 4:
                const data = {
                    purchaseOrder:{
                        purchaseOrderNumber,
                        purchaseOrderState:1,
                        ConsigneeState:1,//收发货状态，0-未收货，1-已收货，2-部分收货，3-未发货，4-已发货，5-部分发货
                        ConfirmedState:1,//确认状态，0-否，1-是
                        ServiceState:2,//服务状态，0-未使用，1-施工中，2-服务完成；
                    },
                    purchaseorderattribute:{
                        IsScore:0,//是否评分标志，0-否，1-是
                    }
                }
                orderUpdate(data).then(res => {
                    //确认收货后更新列表
                    if (res.data.errcode == 0) {
                        this.getData()
                    }
                });
                break;
            default:
                break;
        }
    }
    render() {
        const { stateBtntextMap } = this.props;
        const {
            parttopurchase,
            purchaseorder,
            deliverytopurchase
        } = this.props.orderDetail;
        const orderNum = this.props.match.params.id;
        const state = purchaseorder.state;

        return (
            <div className="orderDetail container">
                <div className="detail-header">
                    <h4>
                        当前状态已完成：
                        <strong className={state == 5 ? "complete" : ""}>
                            {this.stateMap[state]}
                        </strong>
                    </h4>
                    {state == 5 ? (
                        <p>
                            注意：交易已完成，如果您还未收到商品，请及时联系客服处理
                        </p>
                    ) : null}
                    {stateBtntextMap[state] ? (
                        <Button className={state == 1 ? "toPay" : "confirmBtn"} onClick={()=>{
                            this.handleBtnClick(state)
                        }}>
                            {stateBtntextMap[state]}
                        </Button>
                    ) : null}
                    {/* <Button>确认收货</Button> */}
                </div>
                <div className="detail-con">
                    <div className="orderInfo">
                        <h4>订单信息</h4>
                        <p>
                            <span className="mr5">订单编号：{orderNum}</span>

                            <span className="mr5">
                                提交时间：{purchaseorder.createTime}
                            </span>

                            <span className="mr5">
                                付款时间：{purchaseorder.paymentTime}
                            </span>

                            <span className="mr5">完成时间：</span>

                            <span className="mr5">
                                付款方式：
                                {this.payMap[purchaseorder.paymentMethod]}
                            </span>
                        </p>
                    </div>
                    <div className="expressInfo">
                        <h4>物流信息</h4>
                        <div className="expressInfo-con">
                            <div className="expressCon-part">
                                <p className="expressP">
                                    {" "}
                                    收货人：{purchaseorder.consignee}{" "}
                                    {purchaseorder.consigneeMobile}
                                </p>
                                <p className="expressP">
                                    {" "}
                                    订单单号：{orderNum}
                                </p>
                                <p className="expressP">
                                    收货地址：{purchaseorder.deliveryAddress}
                                </p>

                                <p className="expressP">
                                    发货方式：
                                    {
                                        this.shippingMethodMap[
                                            purchaseorder.shippingMethod
                                        ]
                                    }
                                </p>

                                <p className="expressP">
                                    发运单号：
                                    {deliverytopurchase.length
                                        ? deliverytopurchase[0].ExpressNumber
                                        : null}
                                </p>
                            </div>
                            {/* <div>
                                <TimeLine mainNode>
                                    <div>
                                        2018-11-14/周三{" "}
                                        <span className="hmsTime">
                                            14:57:52
                                        </span>{" "}
                                        您的订单已签收
                                    </div>
                                </TimeLine>
                                <TimeLine>
                                    <span className="hmsTime">14:57:52</span>{" "}
                                    您的订单已签收
                                </TimeLine>
                                <TimeLine>
                                    <span className="hmsTime">14:57:52</span>{" "}
                                    您的订单已签收
                                </TimeLine>
                                <TimeLine>
                                    <span className="hmsTime">14:57:52</span>{" "}
                                    您的订单已签收
                                </TimeLine>
                                <TimeLine mainNode>
                                    2018-11-14/周三
                                    <span className="hmsTime">
                                        14:57:52
                                    </span>{" "}
                                    您的订单已签收
                                </TimeLine>
                                <TimeLine>
                                    <span className="hmsTime">14:57:52</span>{" "}
                                    您的订单已签收
                                </TimeLine>
                            </div> */}
                        </div>
                    </div>
                    <div className="goodsInfo">
                        <h4>商品列表</h4>
                        <OrderItem
                            parttopurchase={parttopurchase}
                            purchaseorder={purchaseorder}
                            confirmCallback={this.getData}
                        />
                    </div>
                </div>
            </div>
        );
    }
}

export default OrderDetail;
