import axios from "axios";

export const login = (data,noErr=false) => {
    return axios({
        url: 'auth/customer/login',
        method: 'post',
        data,
        noErr
    })
}
//获取个人信息
export const getUserInfo = (data) => {
    return axios({
        url: 'auth/customer/query',
        method: 'post',
        data
    }).then(res => res, err => err)
}


