import React, { Component } from 'react';
import { Tabs, Table, Input  } from 'antd';
import _ from "lodash"
import {Link} from 'react-router-dom'
import './offer.scss'
import {connect} from 'react-redux';
import {inqueryList,inquiryInit} from "../actions"
const TabPane = Tabs.TabPane;
class Offer extends Component{
  constructor(props){
    super(props)
    this.state={
      inquiryState:'100',  //10询价中，20已报价，40已失效，100所有询价单
      vincode:'',
      partsname:'',
      querycode:'',
      queryname:'',
      pageNum:1,
      pageSize:10,
      showManList:[],
      timer:null,
      showList:[],
      columns:[
        {
          title: '询价单号',
          dataIndex: 'inquiryno',
          width: 120,
        }, {
          title: '询价内容',
          dataIndex: 'description',
          width: 250,
        }, {
          title: '状态',
          dataIndex: 'stausDesc',
          width: 70,
        }, {
          title: 'vin码',
          dataIndex: 'vin',
          width: 110,
        },{
          title: '车牌号',
          dataIndex: 'license',
          width: 100,
        },{
          title: '车型',
          dataIndex: 'vehicletype',
          width: 160,
        },{
          title: '询价人',
          dataIndex: 'contactname',
          width: 110,
        },{
          title: '询价时间',
          dataIndex: 'quiryTime',
          width: 160,
        },{
          title: '操作',
          key: 'actionDesc',
          width: 90,
          render: (text, record, index) => {
            let path=''
            if(text.status==40){
              path=`/inquiry/${text.inquiryno}`
            }else{
              path=`/offerDetail/${text.inquiryno}`
            }
            return (<Link className="link-other" to={path} href="javascript:void(0)">{record.actionDesc}</Link>)
          }
          
        }
      ]
    }
    this.searchName=this.searchName.bind(this)
    this.searchQuery=this.searchQuery.bind(this)
    this.selectMan=this.selectMan.bind(this)
    this.getPage=this.getPage.bind(this)
    this.selectBanner=this.selectBanner.bind(this)
  }
  //选择导航
  selectBanner(key){
    let {pageSize,} = this.state
    let {bannerList,getQuetyList} = this.props
    let banner=bannerList.filter(item=>{
      return item.id===key
    })[0]
    this.setState({
      vincode:'',
      partsname:'',
      querycode:'',
      queryname:'',
      pageNum:1,
      inquiryState:banner.id
    })
    getQuetyList({
      vin:'',
      partsName:'',
      inquiryNo:'',  
      inquirer:'',
      status:key,
      pageId:1,
      pageSize:pageSize     
    })
  }
  searchQuery(e){
    let key=e.target.dataset.key
    let value=e.target.value
    if(key!='queryname'){
      this.setState({
        [key]:value
      })
    }
  }
  searchName(e){
    let manList = _.cloneDeep(this.props.manList)
    let key=e.target.dataset.key
    let value=e.target.value
    this.setState({
      [key]:value
    })
    let valueList=value.split('')
    let len=valueList.length
    let showList=[]
    clearTimeout(this.state.timer)
    let timer=setTimeout(()=>{
      manList.filter(item=>{
        let isMach=0
        for(let i=0;i<len;i++){
          if(item.contactname.indexOf(valueList[i])==-1){
            break
          }else{
            isMach++
          }
        }
        if(isMach==len){
          showList.push(item)
        }
      })
      this.setState({
        showList:showList
      })
    },500)
    this.setState({
      timer:timer
    })
    console.log(this.props)
  }
  selectMan(e){
    let showList = this.state.showList
    let key=e.target.dataset.key
    let value=showList.filter(item=>{
      return item.id==key
    })[0]
    this.setState({
      queryname:value.contactname,
      showList:[]
    })
  }
  searchBtn=()=>{
    let {getQuetyList}=this.props
    let {inquiryState,vincode,partsname,querycode,queryname,pageSize} =this.state
    this.setState({
      pageNum:1
    })
    getQuetyList({
      vin:vincode,
      partsName:partsname,
      inquiryNo:querycode,  
      inquirer:queryname,
      status:inquiryState,
      pageId:1,
      pageSize:pageSize   
    })
  }
  getPage(page){
    let {getQuetyList}=this.props
    let {inquiryState,vincode,partsname,querycode,queryname,pageSize} =this.state
    let pageNum=page.current
    this.setState({
      pageNum:pageNum
    })
    getQuetyList({
      vin:vincode,
      partsName:partsname,
      inquiryNo:querycode,  
      inquirer:queryname,
      status:inquiryState,
      pageId:pageNum,
      pageSize:pageSize   
    })
  }
  componentDidMount(){
    let {initData} = this.props;
    let pageData=this.state
    let params={
      vin:pageData.vincode,
      partsName:pageData.partsname,
      inquiryNo:pageData.querycode,  
      status:pageData.inquiryState,
      pageId:pageData.pageNum,
      pageSize:pageData.pageSize,
      inquirer:pageData.queryname            
    }
    initData(params)
  }
  componentWillUnmount(){
    clearTimeout(this.state.timer)
  }
  render(){
    console.log(this.props)
    let {inquiryState,showList,columns,vincode,partsname,querycode,queryname} = this.state
    let {inqueryList,bannerList} = this.props
    let banner=bannerList.filter(item=>{
      return item.id===inquiryState
    })[0]
    let count=0;
    if(banner){
      count=banner.count
    }
    let pagination={
      showQuickJumper:true,
      defaultCurrent:1,
      hideOnSinglePage:true,
      total:count
    }
    return(
      <div className="container offer-wrap">
        <Tabs defaultActiveKey={inquiryState} size={'large'} onTabClick={this.selectBanner}>
        {
          bannerList.map(item=>(
            <TabPane tab={<span>{item.name}<i className="offer-count">{item.id*1===100?'':item.count}</i></span>} key={item.id} ></TabPane>
          ))
        }
        </Tabs>
        <div className="offer-search">
          <dl className="offer-condition">
            <dt className="name">VIN码:</dt>
            <dd className="offer-query">
              <Input data-key="vincode" value={vincode} onChange={this.searchQuery} />
            </dd>
            <dt className="name">配件名称:</dt>
            <dd className="offer-query">
              <Input data-key="partsname" value={partsname} onChange={this.searchQuery}/>
            </dd> 
            <dt className="name">询价单号:</dt>
            <dd className="offer-query">
              <Input data-key="querycode" value={querycode} onChange={this.searchQuery} />
            </dd>
            <dt className="name">询价人:</dt>
            <dd className="offer-query">
              <Input data-key="queryname" value={queryname}  onChange={this.searchName}/>
              <ul className="name-wrap">
                {
                  showList.length?showList.map(item=>{
                    return (
                      <li className="inquery-name" key={item.id} data-key={item.id} onClick={this.selectMan}>{item.contactname}</li>
                      // <li className="inquery-name">水电费会计师考虑的发挥</li>
                    )
                  }):null
                }
              </ul>
            </dd>
          </dl>
          <button className="offer-btn" onClick={this.searchBtn}>搜索</button>
        </div>
        <Table className="offer-table" bordered pagination={pagination} dataSource={inqueryList} columns={columns} onChange={this.getPage}/>
      </div>
    )
  }

}
const mapStateToProps = (state) => {
  let {Offer_reducer:offerData}=state
  return ({
    manList: offerData.inqueryMan,
    inqueryList:offerData.inqueryList,
    bannerList:offerData.bannerList,
  })
}
const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    initData:(params) => dispatch(inquiryInit(params)),
    getQuetyList:(params) => dispatch(inqueryList(params))
  }
}
export default connect(mapStateToProps,mapDispatchToProps)(Offer)