import { axios } from '@/config/api';
//获取询价单数量
export const getInquiryNum = (data={}) => {
  return axios({
      url: '/Appdata/GetInquiryCount.aspx',
      method: 'post',
      thirdInterface:true,
      data
  }).then(res => res, err => err)
}
export const getInquiryList = (data={}) => {
  return axios({
      url: '/Appdata/GetInquiryList.aspx',
      method: 'post',
      thirdInterface:true,
      data
  }).then(res => res, err => err)
}
//询价人列表
export const getInquiryMan = (data={}) => {
  return axios({
      url: '/Appdata/GetInquiryer.aspx',
      method: 'post',
      thirdInterface:true,
      data
  }).then(res => res, err => err)
}