import React, { Component } from 'react';
import './index.scss'
class CarInfo extends Component{
  constructor(props){
    super(props)
    this.state={
    }
  }
  render(){
    // WBA1J1104FV302907
    let {carInfo}=this.props
    const numbers = [
        {name:'车品牌',value:carInfo.brand},
        {name:'子品牌',value:carInfo.sonBrand},
        {name:'车系',value:carInfo.carVehicle},
        {name:'排量',value:carInfo.outputVolume+'T'},
        {name:'生产地区',value:carInfo.producingArea},
        {name:'生产年份',value:carInfo.producingYear},
        {name:'车身类型',value:carInfo.bodyType},
        {name:'变速箱种类',value:carInfo.gearboxType},
        {name:'驱动形式',value:carInfo.drivingForm},
        {name:'发动机型号',value:carInfo.engineModel},
        {name:'底盘号',value:carInfo.chassisNumber},
      ];
    const listItems = numbers.map((item,index) =>
      <li className="info-item" key={index}>
        <span className="item-name">{item.name}</span>
        <span className="item-value">{item.value}</span>
      </li>
    );
    return(
      <div className="car-wrap">
        {
          carInfo.hasOwnProperty('brand')&&carInfo.brand?
          (<div className="car-info"> 
            <img className="car-logo" src={carInfo.carUrl} alt=""/>
            <div className="carinfo-wrap">
              <p className="car-desc">{carInfo.brand} {carInfo.carVehicle} {carInfo.yearname}款 {carInfo.salesname}</p>
              <ul className="info-wrap">{listItems}</ul>
            </div>
          </div>):null
        }
      </div>
    )
  }

}

export default CarInfo