import { createAction } from "redux-actions";
import * as TYPES from "./actionTypes";
import * as LOGIN from "./api";
import{Cookie} from 'util/util.js'

export const setUserinfo = createAction(TYPES.SET_USERINFO); //订单列表

export const getUserInfo = data=>dispatch=>{
  return  LOGIN.getUserInfo(data).then(res=>{
        if (res.data.errcode == 0) {
            const data = {
                customer: res.data.customer,
                customeraccount: res.data.customeraccount
            };
            Cookie.setCookie('__hksuserinfo__',JSON.stringify(data),3)
            dispatch(setUserinfo(data));
            return true
        }
    })
}

