import React, { Component } from "react";

import ValiPhone from "./components/ValiPhone";
import ValiAccount from "./components/ValiAccount";
import ValiCompany from "./components/ValiCompany";
import "./index.scss";
class Register extends Component {
    constructor(props) {
        super(props);
        this.state = {
            // step: 1
        };
        
    }
    toLogin=()=>{
        this.props.history.push('/login')
    }
    render() {
        const {id} = this.props.match.params
        console.log(id,typeof id);
        
        const mapComs = {
            1:ValiPhone,
            2:ValiAccount,
            3:ValiCompany
        }
        const Child = mapComs[id]
        return (
            <div className="register container">
                {/* 头部 */}
                <div className="register-header">
                    <span className="left">
                        <i className="square" />
                        用户注册
                    </span>
                    <span className="right">
                        已有账号，
                        <i className="tip" onClick={this.toLogin}>立即登录</i>
                    </span>
                </div>
                <div className="register-container">
                    <div className="register-content">
                        <div className="register-content-top">
                            <span className={id ==='1' ? "active" : null}>
                                手机验证
                                <i className="cir" />
                            </span>
                            <span className={id === '2' ? "active" : null}>
                                账号信息
                                <i className="cir" />
                            </span>
                            {/* <span className={id === '3' ? "active" : null}>
                                公司信息
                                <i className="cir" />
                            </span> */}
                        </div>
                        
                        {/* <ValiPhone></ValiPhone> */}
                        {/* <ValiAccount></ValiAccount> */}
                        {/* <ValiCompany></ValiCompany> */}
                        <Child   ></Child>
                    </div>
                    <div className="register-footer">
                        <p>
                            欢迎加入全国领先的全车件供应平台，本平台提供全行业最完整的全车件的配件信息，并以正品保障、快速配送等优质服务，为您提供与众不同的采购体验。
                        </p>
                        <p>1.申请企业会员需要上传营业执照，并提交认证。</p>
                        <p>
                            2.我们将根据您的实际情况审核并认证，通过认证后，享受相应的服务。
                        </p>
                        <p>
                            3.认证成功后平台将发送信息到您注册的手机上，请注意查收。
                        </p>
                        <p>4.如有疑问，欢迎拨打客服电话：4000911900</p>
                    </div>
                </div>
            </div>
        );
    }
}

export default Register;
