import _ from "lodash"
import {
  INQUERY_INIT,
  INQUERY_LIST
} from "./actionTypes.js"
const initState={
  bannerList:[],
  inqueryList:[],
  inqueryMan:[]
}
export default function Offer_reducer(state=initState, action){
  console.log(action)
  let data=action.payload
  switch(action.type){
    case INQUERY_INIT:
      return Object.assign(
        {},
        state,
        {bannerList:_.cloneDeep(data.bannerList)},
        {inqueryList:_.cloneDeep(data.orderList)},
        {inqueryMan:_.cloneDeep(data.manList)}
      )
    case INQUERY_LIST:
      return Object.assign({},state,{inqueryList:_.cloneDeep(data)})
    default: 
      return state
  }
}