import {createAction} from 'redux-actions'
import * as TYPES from './actionTypes'
import * as ORDER from './api'
import { getOrderDetail } from "cont/orderDetail/api";


export  const payList = createAction(TYPES.PAY_LIST)//增加地址
export  const orderDetail = createAction(TYPES.ORDER_DETAIL)//增加地址


export const getPayList = (data)=>dispatch=>{//本来应该是请求数据，测试
    return ORDER.getPayList(data).then(res=>{
        if (res.data.code == 0) {
            const paylist = res.data.payments
            dispatch(payList(paylist))      
        }
    })
}
export const getDetail = (data)=>dispatch=>{//本来应该是请求数据，测试
    return getOrderDetail(data).then(res=>{
        if(res.data.errcode==0){
            const detail = {
                parttopurchase:res.data.parttopurchase,
                purchaseorder:res.data.purchaseorder,
                manHourToPurchase:res.data.manHourToPurchase,
                deliverytopurchase:res.data.deliverytopurchase,
                vehicle:res.data.vehicle,
            }
            dispatch(orderDetail(detail))
        }
    })
}

