import {getInquiryNum,getInquiryList,getInquiryMan} from './offerApi.js';
import {INQUERY_INIT,INQUERY_LIST} from './actionTypes.js';
import moment from 'moment';

function setInueryList(list){
  return {type:INQUERY_LIST,payload:list}
}
function initData(init) {
  return {type:INQUERY_INIT,payload:init}
}

export function inquiryInit (param){
  return async (dispatch,getState)=>{
    let customerID=getState().Login_reducer.hks_userInfo.customeraccount.customerAccountID
    let custumer={cusid:customerID,productSource:2}
    let params=Object.assign({},param,custumer)
    let _list = await getInquiryList(params)
    let _numObj = await getInquiryNum(custumer)
    let _manList = await getInquiryMan(custumer)
    let orderList=[],bannerList=[],manList=[]
    if(_list){
      orderList=_list.inquiryList
      if(orderList&&orderList.length){
        _exchangeData(orderList)
      }
    }
    // if(_numObj){
      let {allInquiry=0,inInquiryCount=0,quotedCount=0,invalidCount=0}=_numObj?_numObj:{}
      let list=[
        {name:'所有询价单',id:'100',count:allInquiry},
        {name:'询价中',id:'10',count:inInquiryCount},
        {name:'已报价',id:'20',count:quotedCount},
        {name:'已失效',id:'40',count:invalidCount}
      ]
      bannerList=list
    // }
    if(_manList){
      manList=_manList.inquiryerList
      if(manList&& manList.length){
        manList.forEach((item,index)=>{
          item.id=index
        })
      }
      
    }
    dispatch(initData({orderList,bannerList,manList}))
  }
}

//分页获取列表数据或快速检索数据
export function inqueryList (param){
  return async (dispatch,getState)=>{
    console.log(getState())
    let customerID=getState().Login_reducer.hks_userInfo.customeraccount.customerAccountID
    let params=Object.assign({},param,{cusid:customerID,productSource:2}) 
    let list = await getInquiryList(params)
    let orderList=[]
    if(list){
      orderList=list.inquiryList
      if(orderList&&orderList.length){
        _exchangeData(orderList)
      }
    }
    dispatch(setInueryList(orderList))
  }
}

//处理列表数据格式
const _exchangeData=(data)=>{
  data.forEach((item,index)=>{
    // debugger
    let stausDesc='',actionDesc=''
    // if(index==0){
    //   item.status=10
    // }else if(index==1){
    //   item.status=20
    // }else{
    //   item.status=40
    // }
    item.quiryTime=moment.unix(item.ttime).format('YYYY-MM-DD HH:mm:ss')
    // debugger
    switch(item.status*1){
      case 10:
        stausDesc='询价中'
        actionDesc='查看'
        break;
      case 20:
        stausDesc='已报价'
        actionDesc='查看'
        break;
      case 40:
        stausDesc='已失效'
        actionDesc='重新询价'
        break;
      default:
    }
    item.stausDesc=stausDesc
    item.actionDesc=actionDesc
  })
}