import axios from 'axios'

//发送验证码
export const sendCode = (data,noErr) => {
    return axios({
        url: 'auth/shortMessage/sendCode',
        method: 'post',
        data,
        noErr
    })
}

//验证验证码
export const checkCode = (data) => {
    return axios({
        url: 'auth/shortMessage/checkCode',
        method: 'post',
        data
    })
}


//注册账号
export const register = (data,noErr=false) => {
    return axios({
        url: 'auth/customer/register',
        method: 'post',
        data,
        noErr
    })
}