import React, { Component } from 'react';
import moment from 'moment';
import {connect} from 'react-redux'
import _ from "lodash"
import { Checkbox, Radio,Icon,Button,Input, DatePicker,message,Modal,Spin } from 'antd';
import Carinfo from './carInfo'
import SelectCar from "./selectCar"
import Parts from './parts'
import {saveOrder,queryAgain} from '../api.js'
import {
  getBrandList,
  getSeriesList,
  getStyleList,
  getVinCarInfo,
  getCarInfo,
  getPartsList,
  setVinCarInfo
} from "../actions.js"
import './inquery.scss'
import {IconUrl} from "@/config.js"
const IconFont = Icon.createFromIconfontCN({
  scriptUrl: IconUrl,
});
const CheckboxGroup = Checkbox.Group;
const RadioGroup = Radio.Group;
class Inquiry extends Component{
  constructor(props){
    super(props)
    this.state={
      inquiryno:'',
      vin:'',
      carnumber:'',
      phone:'',
      name:'',
      partsQuality:['品牌','原厂原包','原厂配套','拆车件','再制造件'],
      selectQuality:['品牌','原厂原包','原厂配套'],
      invoice:[
        {label:'不要求发票',value:2},
        {label:'增值税专用发票',value:3},
        {label:'增值税普通发票',value:4}
      ],
      defaultInvoice:2,
      dateFlag:false,
      time:'12312',
      timeText:'',
      vinModalFlag:false,
      vinIndex:0,
      isQuiry:false
    }
    this.checkQuality=this.checkQuality.bind(this)
    this.changeRnvoice=this.changeRnvoice.bind(this)
    this.searchVin=this.searchVin.bind(this)
    this.issueInquiry=this.issueInquiry.bind(this)
    this.pickerBlur=this.pickerBlur.bind(this)
    this.vinOk=this.vinOk.bind(this)
    this.vinCancel=this.vinCancel.bind(this)
  }
  checkQuality(checkedValues) {
    console.log('checked = ', checkedValues);
    this.setState({
      selectQuality:checkedValues
    })
  }
  changeRnvoice(e){
    this.setState({
      defaultInvoice:e.target.value
    })
  }
  componentDidMount(){
    let {getBrandList,match,getPartsList,userInfo,getStyleList} = this.props
    getBrandList()
    let time=moment().add(1,'days').unix()
    let timeText=moment().add(1,'days').format('YYYY-MM-DD HH:mm:ss')
    if(match.params.hasOwnProperty('id')){  //待有询价单号操作
      let params={
        productSource:2,
        cusid:userInfo.customeraccount.customerAccountID,
        inquiryNo:match.params.id
      }
      queryAgain(params).then(res=>{
        if(res.success){
          time=res.failuretime
          timeText=moment.unix(res.failuretime*1).format('YYYY-MM-DD HH:mm:ss')
          let partsQuality=res.PartsQuality?res.PartsQuality.split(','):['品牌','原厂原包','原厂配套']
          this.setState({
            inquiryno:match.params.id,
            vin:res.vin,
            carnumber:res.license,
            phone:res.contacttel,
            name:res.contactname,
            defaultInvoice:res.hasTax*1,
            time:time,
            timeText:timeText,
            selectQuality:partsQuality
          })
          //开始查询车型
          if(res.vin){
            this.searchVin({hkscode:res.hkscode})
          }else{
             let data = {
                requestjson: [
                    {
                        rowid: 1,
                        modelbrandid:res.modelbrandid,//256,// 
                        makeid: res.brandid,//298,//
                        modelid: res.classifyid//1507,//
                    }
                ]
              };
              getStyleList(data,res.hkscode)////'HFA20077826'
          }
          res.partsList.push({
            partsname:'',
            oe:'',
            count:1,
            unit:'',
            remark:'',
            tid:''
          })
          getPartsList(res.partsList)
        }
      }).catch(err=>{
        console.log(err)
      })
    }else{
      this.setState({
        time:time,
        timeText:timeText,
        phone:userInfo.customeraccount.phoneNumber,
        name:userInfo.customeraccount.accountName,
      })
    }
    
  }
  componentWillUnmount(){
    let {getPartsList,getCarInfo}=this.props
    let partList=[]
    for(let i=0;i<4;i++){
      partList.push({
        partsname:'',
        oe:'',
        count:1,
        unit:'',
        remark:'',
        tid:''
      })
    }
    getPartsList(partList)
    getCarInfo({})
  }
  componentWillReceiveProps(nextProps){
    if(nextProps.carInfo.hasOwnProperty('vin')){
      this.setState({vin:nextProps.carInfo.vin})
    }
    console.log(nextProps)
  }
  disabledDate=(current)=> {
    return current && current < moment().endOf('day');
  }
  openDate=()=>{
    let {dateFlag} = this.state;
    dateFlag=!dateFlag
    this.setState({
      dateFlag:dateFlag
    })
  }
  confirmDate=(date)=>{
    let time=moment(date).unix()
    let timeText=moment(date).format('YYYY-MM-DD HH:mm:ss')
    let {dateFlag} = this.state;
    dateFlag=!dateFlag
    this.setState({
      dateFlag:dateFlag,
      time:time,
      timeText:timeText
    })
  }
  changeInput=(e)=>{
    let key=e.target.dataset.name;
    let value=e.target.value
    this.setState({
      [key]:value
    })
  }
  //vin码查询
  searchVin(isHkscode){
    let {getVinCarInfo} = this.props
    let params={
      vin:this.state.vin
    }
    this.setState({
      vinModalFlag:true
    })
    if(isHkscode.hasOwnProperty('hkscode')){
      getVinCarInfo(params,isHkscode.hkscode)
    }else{
      getVinCarInfo(params)
    }
    
  }
  //发布询价
  issueInquiry(){
    let {userInfo,carInfo,partsList,history}=this.props
    let {inquiryno,carnumber,phone,name,time,selectQuality,defaultInvoice,isQuiry}=this.state
    let qualityString=selectQuality.join(',')
    let partsInfo=[],detail=[]
    if(!carInfo.hasOwnProperty('brand')){
      Modal.warning({
        title: '这是一个提示信息',
        content: '请输入IVN码查车型或选择车型信息',
      });
      return
    }
    partsList.forEach(item=>{
      if(item.partsname.length){
        partsInfo.push(item)
      }
    })
    if(!partsInfo.length){
      Modal.warning({
        title: '这是一个提示信息',
        content: '请录入配件信息',
      });
      return
    }
    let unitList=partsInfo.filter(item=>{
      return !item.unit
    })
    if(unitList.length){
      Modal.warning({
        title: '这是一个提示信息',
        content: '请录入配件单位信息',
      });
      return
    }
    partsInfo.forEach(item=>{
      detail.push({
        tid:'',
        partsname:item.partsname,
        remark:item.remark,
        unit:item.unit,
        count:item.count
      })
    })
    let params={
      detail:detail,
      inquiryno: inquiryno,
      license: carnumber,
      inquirytype: "快速询价",
      failuretime:time,
      hasTax: defaultInvoice,
      PartsQuality :qualityString,//用英文逗号隔开
      cusTrueName:name,
      contacttel :phone,
      inquirysource :"WEB",//询价
    }
    let params1={
        functype:'inquiry',
        inquirysourcetype:2,
        cusid:userInfo.customeraccount.customerAccountID,
        // province: userInfo.province,
        // city: userInfo.city,
        // area: userInfo.area,
        // cid: userInfo.cid,
        provincecode: userInfo.customer.areaID.toString().substr(0,2),
        citycode: userInfo.customer.areaID.toString().substr(0,4),
        areacode: userInfo.customer.areaID,
        zonecode:userInfo.customer.cID,
    }
    let params2={
        vin: carInfo.vin,
        hkscode: carInfo.hkscode,
        vehicletype: carInfo.cartype, //"车型",
        modelbrandid: carInfo.modelbrandid,//"车型品牌id",
        brand:carInfo.brand, //'车型品牌名称',
        makeid:carInfo.makeid, //'车型厂家编号',
        makename:carInfo.makename, //'车型厂家名称',
        classify:carInfo.classify, //'车系名称',
        classifyid:carInfo.classifyid, //'车系名称',
        cartype:carInfo.cartype, //'车型名称',
        VehicleType:carInfo.brand +' '+carInfo.carVehicle+' '+ carInfo.producingYear+'款 ' + carInfo.salesname
    }
  let allParams=Object.assign(params,params1,params2)
  this.setState({
    isQuiry:true
  })
  saveOrder(allParams).then(res=>{
    this.setState({
      isQuiry:false
    })
    if(res.success){
      history.push({
        pathname: '/offer',
      })
    }else{
      message.error(res.msg)
    }
    
    console.log(res)
  }).catch(err=>{
    console.log(err)
  })
 
  console.log(allParams)
  }
  pickerBlur(){
    this.setState({
      dateFlag:false
    })
  }
  vinOk(){
    let {setVinCarInfo} = this.props
    let {vinIndex} =this.state
    this.setState({
      vinModalFlag:false
    })
    setVinCarInfo(vinIndex)
  }
  vinCancel(){
    let {setVinCarInfo} = this.props
    this.setState({
      vinModalFlag:false,
      vinIndex:0
    })
    setVinCarInfo(0)
  }
  selectVin(index){
    this.setState({
      vinIndex:index
    })
  }
  render(){
    let {vinCars,carInfo,styleList,brandList,vehicleList,
      character,partsList,getSeriesList,getStyleList,getCarInfo,getPartsList,
    } = this.props
    let {carnumber,phone,name,partsQuality,selectQuality,invoice,defaultInvoice,dateFlag,timeText,vinModalFlag,vinIndex,isQuiry} = this.state
    return(
      <div className="container inquery">
        <div className="car-wrap">
          <p className="module-info">
            <span className="module-name">车辆信息</span>
            {/* <span className="module-other">草稿箱（1）</span> */}
          </p>
          <div className="search-car">
            <div className="serach-wrap">
              <span>VIN码: </span>
              <Input data-name="vin" value={this.state.vin} onChange={this.changeInput}></Input>
              <Button type="primary" className="search-btn" onClick={this.searchVin}>查询</Button>
              <span className="perhaps">或者</span>
              <SelectCar 
                getSeriesList={getSeriesList} 
                getStyleList={getStyleList} 
                getCarInfo={getCarInfo} 
                styleList={styleList}
                brandList={brandList}
                vehicleList={vehicleList}
                character={character}
              ></SelectCar>
            </div>
            <div>
              <span className="car-number">车牌号:</span>
              <Input data-name="carnumber" onChange={this.changeInput} value={carnumber}></Input>
            </div>
          </div>
        </div>
        <Carinfo carInfo={carInfo}></Carinfo>
        <Parts partsList={partsList} getPartsList={getPartsList}></Parts>
        <div className="requirement">
          <p className="module-info">
            <span className="module-name">其他要求</span>
          </p>
          <dl className="option-wrap">
            <dt className="option-name">配件品质：</dt>
            <dd className="opstions">
              <CheckboxGroup options={partsQuality} value={selectQuality} onChange={this.checkQuality} />
            </dd>
            <dt className="option-name">发票要求：</dt>
            <dd className="opstions">
              <RadioGroup options={invoice} value={defaultInvoice} onChange={this.changeRnvoice} />
            </dd>
            <dt className="option-name">询价时效：</dt>
            <dd className="opstions">
              {
                !dateFlag?(<span>{timeText}前有效</span>):(
                  <DatePicker
                    format="YYYY-MM-DD HH:mm:ss"
                    // disabledDate={this.disabledDate}
                    // disabledTime={this.disabledDateTime}
                    showToday={false}
                    open={this.state.dateFlag}
                    showTime={{ defaultValue: moment('00:00:00', 'HH:mm:ss') }}
                    onOk={this.confirmDate}
                    onBlur={this.pickerBlur}
                  />
                )
              }
              <IconFont type="icon-shijian" className="shi-jian" onClick={this.openDate}></IconFont>
            </dd>
          </dl>
        </div>
        <div className="contact">
          <p className="module-info">
            <span className="module-name">联系方式</span>
          </p>
          <dl className="contact-info">
            <dt className="contact-desc">联系人：</dt>
            <dd>
              <Input data-name="name" onChange={this.changeInput} value={name}></Input>
            </dd>
            <dt className="contact-desc contact-space">联系电话：</dt>
            <dd>
              <Input data-name="phone" onChange={this.changeInput} value={phone}></Input>
            </dd>
          </dl>
        </div>
        <div className="inquery-btn">
          <Button type="primary" className="price-btn" onClick={this.issueInquiry}>发布询价</Button>
          {/* <Button>保存草稿</Button> */}
        </div>
        {
          vinCars.length?(
            <Modal
            title="VIN 码查询"
            visible={vinModalFlag}
            onOk={this.vinOk}
            onCancel={this.vinCancel}
          >
          {
            vinCars.map((item,index)=>{
              return (
                <div key={index} className={vinIndex==index?'vin-wrap vin-select':'vin-wrap'}  onClick={this.selectVin.bind(this,index)}>
                  <img src={item.carUrl} alt={item.brand}/>
                  <div>
                    <p>{item.brand} {item.sonBrand}</p>
                    <p>{item.yearname}款 {item.salesname}</p>
                  </div>
                </div>
              )
            })
          }
          </Modal>
          ):null
        }
        {
          isQuiry?(
          <div className="loading">
            <Spin size="large"/>
          </div>):null
        }
      </div>
    )
  }
}
const mapStateToProps=(state)=>{
  let {Inquiry_reducer:inquiryData,Login_reducer:userInfo}=state
  return {
    // userInfo:inquiryData.userInfo,
    carInfo:inquiryData.carInfo,
    // standNames:inquiryData.standNames,
    styleList:inquiryData.styleList,
    brandList:inquiryData.brandList,
    vehicleList:inquiryData.vehicleList,
    character:inquiryData.character,
    vinCars:inquiryData.vinCars,
    partsList:_.cloneDeep(inquiryData.partsList),
    userInfo:_.cloneDeep(userInfo.hks_userInfo)
  }
}
const mapDispatchToProps=(dispatch,ownProps)=>{
  return {
    getBrandList:(params) => dispatch(getBrandList(params)),
    getSeriesList:(params) => dispatch(getSeriesList(params)),
    getStyleList:(params,hkscode) => dispatch(getStyleList(params,hkscode)),
    getVinCarInfo:(params,hkscode) => dispatch(getVinCarInfo(params,hkscode)),
    getCarInfo:(params)=> dispatch(getCarInfo(params)),
    getPartsList:(params)=> dispatch(getPartsList(params)),
    setVinCarInfo:(num)=>dispatch(setVinCarInfo(num)),
  }
}
export default connect(mapStateToProps,mapDispatchToProps)(Inquiry)