function setInquerydetail(obj) {
  return {
    type: INQUERY_DETAIL,
    payload: obj
  }
}

function _actionParts(list) {
  return {
    type: ACTION_PARTS,
    payload: list
  }
}

function inqueryStatus(status) {
  let statusDesc = '';
  switch (status * 1) {
    case 10:
      statusDesc = '询价中'
      break
    case 20:
      statusDesc = '已报价'
      break
    case 40:
      statusDesc = '已失效'
      break
    default:
  }
  return statusDesc
}

function invoiceType(status) {
  let statusDesc = '';
  switch (status * 1) {
    case 2:
      statusDesc = '不开发票'
      break
    case 3:
      statusDesc = '专用发票'
      break
    case 4:
      statusDesc = '普通发票'
      break
    default:
  }
  return statusDesc
}
