//单个下拉和选择配件
export function actionParts(nameKey, oeKey,goodsKey) {
  return (dispatch, getState) => {
    let offerList = _.cloneDeep(getState().offerDetail_reducer.showOffer)
    if(!isNaN(goodsKey)){
      let DetailList=offerList[nameKey].OEList[oeKey].DetailList
      DetailList.forEach(item=>{
        item.radioFlag=false
      })
      DetailList[goodsKey].radioFlag=true
    }else{
      let prvFlag = offerList[nameKey].OEList[oeKey].retractFlag
      offerList[nameKey].OEList[oeKey].retractFlag = !prvFlag
    }
    dispatch(_actionParts(offerList))
  }
}
//全部收起下拉
export function allRetract(allFlag){
  return (dispatch,getState) => {
    let offerList = _.cloneDeep(getState().offerDetail_reducer.showOffer)
    offerList.forEach(name=>{
      name.OEList.forEach(oe=>{
        oe.retractFlag=allFlag
      })
    })
    dispatch(_actionParts(offerList))
  }
}
//筛选选展示配件
export function filterShow(partsQuality,invoice){
  return (dispatch,getState) => {
    let offerList = _.cloneDeep(getState().offerDetail_reducer.offerList)
    offerList.forEach(name=>{
      for(let i=0;i<name.OEList.length;i++){
        let detailList=name.OEList[i].DetailList
        let firstList=[],lastList=[]
        detailList.forEach((parts,index)=>{
          if(partsQuality.length){
            partsQuality.forEach(quality=>{
              if(quality==parts.PartsQuality){
                firstList.push(parts)
              }
            })
          }else{
            firstList.push(parts)
          }
        })
        if(firstList.length){
          firstList.forEach(voice=>{
            if(invoice&&voice.InvoiceType==2){
              lastList.push(voice)
            }else if(!invoice&&(voice.InvoiceType==3||voice.InvoiceType==3)){
              lastList.push(voice)
            }
          })
        }
        if(lastList.length){
          detailList=lastList
        }else{
          name.OEList.splice(i,1)
          --i
        }
      }
      // name.OEList.forEach((oe)=>{
      //   let firstList=[],lastList=[]
      //   oe.DetailList.forEach((parts,index)=>{
      //     if(partsQuality.length){
      //       partsQuality.forEach(quality=>{
      //         if(quality==parts.PartsQuality){
      //           firstList.push(parts)
      //         }
      //       })
      //     }else{
      //       firstList.push(parts)
      //     }
      //   })
      //   if(firstList.length){
      //     firstList.forEach(voice=>{
      //       if(invoice&&voice.InvoiceType==2){
      //         lastList.push(voice)
      //       }else if(!invoice&&(voice.InvoiceType==3||voice.InvoiceType==3)){
      //         lastList.push(voice)
      //       }
      //     })
      //   }
      //   oe.DetailList=lastList
      // })
    })
    dispatch(_actionParts(offerList))
  }
}

//添加购物车
export function addShopCar(){
  return (dispatch,getState) => {
    let shoppingCartList=[];
    let customerID=getState().Login_reducer.hks_userInfo.customeraccount.customerAccountID
    let offerList = _.cloneDeep(getState().offerDetail_reducer.showOffer);
    let inquryInfo = _.cloneDeep(getState().offerDetail_reducer.offerInfo);
    offerList.forEach(name=>{
      name.OEList.forEach(oe=>{
        oe.DetailList.forEach(parts=>{
          if(parts.radioFlag){
            let invoiceType=1,freightType=2
            if(parts.InvoiceType==2){
              invoiceType=0
            }
            if(!parts.FreightType){
              freightType=1
            }
            shoppingCartList.push({
              source:19,  //人工报价19
              partName:name.PartsName,
              partBrandName:parts.PartsBrand,
              partItemID:parts.PartItemId,
              partNumber:parts.OtherPartCode,
              // partSize:'2',
              quoteOrderID:parts.PartsTid||2013,
              partSizeUOM:parts.PartsUnit,
              quantity:1,
              licenseNo:inquryInfo.VehicleNumber,
              price:parts.PartsPrice,
              supplieID:parts.SupplierId,//506,//
              supplieName:parts.SupplierName,
              storeNumber:parts.WarehouseCode,
              inquiryNumber:inquryInfo.InquiryNo,
              isNeedInvoice:invoiceType,
              freightPaymentMethod:freightType,
              warrantPeriod:parts.Warrantyperiod,
              timeToPay:parts.DeliveryTime,
              hkscode:inquryInfo.HksCode,
              vehicleID:inquryInfo.Classifyid,
            })
          }
        })
      })
    })
    if(!shoppingCartList.length){
      message.warning('请先选择配件');
      return
    }
    let params={
      customerAccountID:customerID,
      shoppingCartList:shoppingCartList
    }
    addShop(params).then(res=>{
      if(res.data.errcode==0){
        message.success(res.data.errmsg,2)
      }
      console.log(res)
    })
  }
}