import React, { Component } from 'react';
import {withRouter} from "react-router-dom"
import OutFooter from "../outFooter/index.js"
import InsideFooter from "../insideFooter/index.js"
class ValidateFooter extends Component{
  componentWillMount(){
    // this.props.history.push('/home')
  }
  render(){
    // console.log(this.props)
    const {location}=this.props
    let isLogin=true;
    if(location.pathname === '/login'||location.pathname === '/register'){
      isLogin=false
    }
    return(
      <React.Fragment>
        {isLogin ? (<InsideFooter></InsideFooter>):(<OutFooter></OutFooter>)}
      </React.Fragment>
    ) 
  }

}

export default withRouter(ValidateFooter)