import { createAction } from "redux-actions";
import * as TYPES from "./actionTypes";
import * as DETAIL from './api.js'

export const orderDetail = createAction(TYPES.ORDER_DETAIL)
export const getOrderDetail = data=>dispatch=>{
    DETAIL.getOrderDetail(data).then(res=>{
        if(res.data.errcode==0){
            const detail = {
                parttopurchase:res.data.parttopurchase,
                purchaseorder:res.data.purchaseorder,
                manHourToPurchase:res.data.manHourToPurchase,
                deliverytopurchase:res.data.deliverytopurchase,
                vehicle:res.data.vehicle,
            }
            dispatch(orderDetail(detail))
        }
    })
}
