import React, {Component}from "react"
import {Input,Anchor,BackTop} from 'antd'
import moment from 'moment';
import './index.scss'
const Search = Input.Search;
const { Link } = Anchor;
const handleClick = (e, link) => {
  e.preventDefault();
};
class OfferRequire extends Component{
  constructor(props){
    super(props)
    this.state={
      showList:[]
    }
    this.searchName=this.searchName.bind(this)
  }
  componentWillReceiveProps(nextProps){
    let {leftList}= nextProps
    this.setState({
      showList:leftList
    })
  }
  searchName(e){
    let {leftList}= this.props
    let showList=leftList
    let valueList=e.target.value.split('')
    let len=valueList.length
    clearTimeout(this.state.timer)
    let timer=setTimeout(()=>{
      // let {leftList}= this.props
      showList=leftList.filter(item=>{
        let isMatch=0
        for(let i=0;i<len;i++){
          if(item.name.indexOf(valueList[i])==-1){
            break
          }else{
            isMatch++
          }
        }
        if(isMatch==len){
          return item
        }
      })
      this.setState({
        showList:showList
      })
    },500)
    this.setState({
      timer:timer,
    })
  }
  componentWillUnmount(){
    clearTimeout(this.state.timer)
  }
  render(){
    let {offerInfo} = this.props
    let {showList} =this.state
    return(
      <React.Fragment>
        <div className="offer-State">
          <button className="state-btn">{offerInfo.inquiryDesc}</button>
          <p className="offer-number">询价单：{offerInfo.InquiryNo}</p>
        </div>
        <div className="offer-car">
          <img className="car-image" src={offerInfo.brandImg} alt=""/>
          <div className="car-desc">
            {offerInfo.VehicleNumber?(<p className="car-number">{offerInfo.VehicleNumber}</p>):null}
            {offerInfo.VIN?(<p>{offerInfo.VIN}</p>):null}
            <p>{offerInfo.VehicleType}</p>
          </div>
        </div>
        <dl className="offer-request ">
          <p className="request-item">
            <span className="request-name">配件品质:</span>
            <span className="request-desc">{offerInfo.PartsQuality}</span>
          </p>
          <p className="request-item">
            <span className="request-name">发票要求:</span>
            <span className="request-desc">{offerInfo.invoiceDesc}</span>
          </p>
          <p className="request-item">
            <span className="request-name">询价时效:</span>
            <span className="request-desc">{moment.unix(offerInfo.FailureTime*1).format('YYYY-MM-DD HH:mm')}前有效</span>
          </p>
          <p className="request-item">
            <span className="request-name">联系人:</span>
            <span className="request-desc">{offerInfo.ContactPerson}</span>
          </p>
          <p className="request-item">
            <span className="request-name">询价时间:</span>
            <span className="request-desc">{moment.unix(offerInfo.InquiryTime*1).format('YYYY-MM-DD HH:mm')}</span>
          </p>
        </dl>
        <div className="offer-parts">
          <Search
            placeholder="配件名称/原厂零件号"
            onChange={value=>this.searchName(value)}
          />
          <Anchor className="name-list" onClick={handleClick}>
          {
            showList.map((item,index)=>{
              return(
                <Link key={item.hrefId} href={item.hrefId} title={(index+1)+'.'+item.name+' ('+item.num+'个报价)'} className="name-item"></Link>
              )
            })
          }
          </Anchor>
        </div>
        <BackTop />
      </React.Fragment>
    )
  }
}
export default OfferRequire