import React, { Component } from "react";
import { Checkbox, Icon, Modal } from "antd";
import Count from "comp/count";
// import { connect } from "react-redux";
// @connect(
//     state => ({userInfo:state.Login_reducer.hks_userInfo})
// )
class PartItem extends Component {
    constructor(props) {
        super(props);
        this.state = {};
        this.freightMap = {
            1: "寄付",
            2: "到付"
        };
    }

    countChange = num => {
        const {
            firstIndex,
            secondIndex,
            updateShopGoods,
            partInfo,
            vehicle,
            customerAccountID
        } = this.props;
        this.props.handChangeNum(firstIndex, secondIndex, num);
        //调取修改购物车接口
        const updateData = {
            shoppingCart: {
                customerAccountID,
                partItemID: partInfo.partItemID,
                quantity: num,
                vehicleID: vehicle && vehicle.vehicleID,
                quoteOrderID: partInfo.quoteOrderID
                // "licenseNo": vehicle&&vehicle.licenseNo
            }
        };
        updateShopGoods(updateData);
    };
    handleChange = e => {
        const checked = e.target.checked;
        const { firstIndex, secondIndex } = this.props;
        this.props.handleChangeForPart(firstIndex, secondIndex, checked);
    };
    //删除购物车
    delGoods = partInfo => {
        // console.log(partInfo);
        // console.log(this.props.vehicle);
        const { checkAll } = this.props;
        const me = this;
        const confirm = Modal.confirm;
        console.log("****************************");
        console.log(partInfo);
        console.log("****************************");
        confirm({
            title: "确定删除该商品？",
            okType: "danger",
            onOk() {
                const { vehicle, delShopGoods, queryData,customerAccountID } = me.props;

                delShopGoods(
                    {
                        customerAccountID,
                        shoppingCartList: [
                            {
                                partItemID: partInfo.partItemID,
                                quoteOrderID: partInfo.quoteOrderID
                                // licenseNo: vehicle.licenseNo
                            }
                        ]
                    },
                    queryData,
                    checkAll
                );
            }
        });
    };
    render() {
        const { partInfo } = this.props;
        //运费
        const freight = partInfo.freightPaymentMethod ? (
            <span className="box">
                运费{this.freightMap[partInfo.freightPaymentMethod]}
            </span>
        ) : null;
        //质保期限
        const warrantPeriod = partInfo.warrantPeriod ? (
            <span className="box">质保{partInfo.warrantPeriod}个月</span>
        ) : null;
        //到货时间
        const arriveTime = partInfo.timeToPay ? (
            <span className="box">{partInfo.timeToPay}</span>
        ) : null;
        //到货时间
        const invoice = partInfo.isNeedInvoice ? (
            <span className="box">含发票</span>
        ) : null;
        const oeHtml = partInfo.partItem.oecode?(<span>原厂零件号：{partInfo.partItem.oecode}</span>):null
        return (
            <div className="partItem box-hover">
                <div>
                    <Checkbox
                        checked={partInfo.checked}
                        onChange={this.handleChange}
                    />
                    <span className="partInfo dib">
                        <span>{partInfo.partName} | </span>
                        {/* <span>原厂零件号：{partInfo.oe}</span> */}
                        {oeHtml}
                    </span>
                    <span className="support dib">{partInfo.supplieName}</span>
                    <span className="price dib">￥{partInfo.price}</span>
                    <span className="count dib">
                        <Count
                            onChange={this.countChange}
                            num={partInfo.quantity}
                        />
                    </span>
                    <span className="unit dib">
                        {!partInfo.partSize || partInfo.partSize == 1
                            ? partInfo.partSizeUOM
                            : partInfo.partSize + partInfo.partSizeUOM}
                    </span>
                    <span className="amount dib warn">
                        ￥{partInfo.price * partInfo.quantity}
                    </span>
                    <Icon
                        type="delete"
                        className="del-icon dib"
                        onClick={() => {
                            this.delGoods(partInfo);
                        }}
                    />
                </div>
                <div>
                    <span className="brand">品牌 {partInfo.partBrandName}</span>
                    <span className="partInfo dib">
                        {invoice}
                        {/* <span className="box">运费{partInfo.freight}</span> */}
                        {freight}
                        {/* <span className="box">
                            {partInfo.ArriveTime}小时内到货
                        </span> */}
                        {arriveTime}
                        {/* <span className="box">质保{partInfo.quality}</span> */}
                        {warrantPeriod}
                    </span>
                </div>
            </div>
        );
    }
}

export default PartItem;
