import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import { Input, Button, Cascader, message } from "antd";
import { connect } from "react-redux";
import Tips from "./Tips";
import { getArea } from "cont/order/api";
import validateHks from "util/validate";
import { register } from "../../api";
import { getUserInfo } from "cont/login/actions";

// 验证手机
@withRouter
@connect(
    s => s.Register_reducer,
    { getUserInfo }
)
class ValiAccount extends Component {
    constructor(props) {
        super(props);
        this.state = {
            options: [],
            addressInfo: {
                areaID: "",
                areaName: ""
            },
            areaError: "",
            customerName: "",
            customerNameError: "",
            password: "",
            passwordError: "",
            confirmPwd: "",
            confirmPwdError: "",
            email: "",
            accountName: "",
            accountNameError: ""
        };
    }

    componentWillMount() {
        this.getAreaData(0, data => {
            this.setState({
                options: data
            });
        });
    }
    areaChange = (value, selectedOptions) => {
        if (value && value.length == 3) {
            let areaName = "";
            const areaID = value[2];
            selectedOptions.forEach(val => {
                areaName += `${val.name} `;
            });
            areaName = areaName.slice(0, areaName.length - 1);

            this.setState(
                {
                    addressInfo: {
                        ...this.state.addressInfo,
                        areaName,
                        areaID
                    },
                    areaError: false
                },
                () => {}
            );
        }
    };
    loadData = selectedOptions => {
        const targetOption = selectedOptions[selectedOptions.length - 1];
        targetOption.loading = true;
        const { id } = targetOption;
        this.getAreaData(id, data => {
            targetOption.loading = false;
            targetOption.children = data;
            this.setState(preState => {
                return {
                    options: [...preState.options]
                };
            });
        });
    };
    getAreaData = (pid, callBack) => {
        getArea({
            areaPrice: {
                appkey: "1",
                pid
            }
        }).then(res => {
            if (res.data.errcode == 0) {
                const data = res.data.areaPriceList,
                    arr = [];
                for (let index = 0; index < data.length; index++) {
                    const element = data[index];
                    if (
                        !element.name.includes("作废") &&
                        element.name != "台湾省" &&
                        element.name != "香港特别行政区" &&
                        element.name != "澳门特别行政区"
                    ) {
                        let obj = {
                            id: element.id,
                            name: element.name
                        };
                        if (element.areaLevel < 3) {
                            obj.isLeaf = false;
                        }
                        arr.push(obj);
                    }
                }
                callBack.call(this, arr);
            }
        });
    };
    handleChange = e => {
        const name = e.target.name,
            value = e.target.value.trim();
        this.setState({
            [name]: value
        });
        //验证错误提示
        const key = `${name}Error`;
        //输入确认密码
        // if (name == "confirmPwd") {
        //     const { password, confirmPwd, confirmPwdError } = this.state;
        //     //如果确认密码不一致
        //     if (password != confirmPwd) {
        //         this.setState({
        //             confirmPwdError: true
        //         });
        //     } else if (confirmPwdError) {
        //         //之前不一致，现在一致
        //         this.setState({
        //             confirmPwdError: false
        //         });
        //     }
        // }
        // if(value&&((this.state[key]===true)||this.state[key]==='')){
        //     this.setState(
        //         {
        //             [key]:false
        //         }
        //     )
        // }
    };
    toComponyInfo = () => {
        const {
            addressInfo,
            customerName,
            accountName,
            password,
            confirmPwd,
            email
        } = this.state;

        const vali = validateHks();
        const valiPwd = () => {
            return confirmPwd == password;
        };
        //验证不能使用手机号格式
        const valiAccountNameByPhone = () => {
            return !/^1[0-9]{10}$/.test(accountName);
        };
        //4-20位数字加字母
        const valiAccountNameRule = () => {
            return /^[0-9a-zA-Z]{4,20}$/.test(accountName);
        };
        vali.add({
            type: "required",
            msg: "所在地址不能为空！",
            val: addressInfo.areaID
        });

        vali.add({
            type: "required",
            msg: "用户名不能为空！",
            val: accountName
        });
        vali.add({
            valid: valiAccountNameByPhone,
            msg: "用户名不能使用手机号格式！"
        });
        vali.add({
            valid: valiAccountNameRule,
            msg: "用户名不能使用手机号格式！"
        });

        vali.add({
            type: "required",
            msg: "昵称不能为空！",
            val: customerName
        });
        vali.add({ type: "required", msg: "密码不能为空！", val: password });
        vali.add({
            type: "required",
            msg: "确认密码不能为空！",
            val: confirmPwd
        });
        vali.add({ msg: "确认密码不一致！", valid: valiPwd });
        const msg = vali.vali();
        if (msg) {
            message.error(msg);

            return;
        }
        const { mobile, getUserInfo, history } = this.props;
        const data = {
            customer: {
                customerName,
                Mobile: mobile || 13732947001,
                Address: addressInfo.areaName,
                Email: email,
                areaID: addressInfo.areaID,
                userSourceID: 4
            },
            customerAccount: {
                accountName,
                phoneNumber: mobile || 13732947001,
                accountPassword: password,
                Email: email
            }
        };
        register(data).then(res => {
            if (res.data.errcode == 0) {
                getUserInfo({ accountName }).then(success => {
                    if (success) {
                        history.push("/home");
                    }
                });
            }
        });
    };
    handleBlur = () => {
        const { accountName } = this.state;
        if(!/^1[0-9]{10}$/.test(accountName)){
            this.setState({
                accountNameError:''
            })
        }else{
            this.setState({
                accountNameError:true
            })
        }
    };
    render() {
        const {
            options,
            areaError,
            customerNameError,
            passwordError,
            confirmPwdError,
            accountNameError
        } = this.state;
        return (
            <div className="vali valiAccount">
                <div>
                    <span className="label-input">
                        <i className="mr5">*</i>所在地
                    </span>
                    <Cascader
                        className="valiAccount-cascader mr15"
                        fieldNames={{
                            label: "name",
                            value: "id",
                            children: "children"
                        }}
                        options={options}
                        placeholder="请选择省市区"
                        changeOnSelect={true}
                        onChange={this.areaChange}
                        loadData={this.loadData}
                    />
                    <Tips error={areaError} />
                </div>
                <div>
                    <span className="label-input">
                        <i className="mr5">*</i>用户名
                    </span>
                    <Input
                        className="mr15"
                        name="accountName"
                        onChange={this.handleChange}
                        onBlur={this.handleBlur}
                        placeholder="4-20位字母或数字组合，不包含特殊符号"
                    />
                    <Tips error={accountNameError} errMsg='用户名不能使用手机号格式'/>
                </div>
                <div>
                    <span className="label-input">
                        <i className="mr5">*</i>昵称
                    </span>
                    <Input
                        className="mr15"
                        name="customerName"
                        onChange={this.handleChange}
                    />
                    <Tips error={customerNameError} />
                </div>
                <div>
                    <span className="label-input">
                        <i className="mr5">*</i>密码
                    </span>
                    <Input
                        className="mr15"
                        name="password"
                        type="password"
                        onChange={this.handleChange}
                    />
                    <Tips error={passwordError} />
                </div>
                <div>
                    <span className="label-input">
                        <i className="mr5">*</i>确认密码
                    </span>
                    <Input
                        className="mr15"
                        type="password"
                        name="confirmPwd"
                        onChange={this.handleChange}
                    />
                    <Tips error={confirmPwdError} />
                </div>
                <div>
                    <span className="label-input">邮箱</span>
                    <Input
                        className="mr15"
                        name="email"
                        onChange={this.handleChange}
                    />
                    {/* <Tips error={error} /> */}
                </div>

                <div className="valiphone-item">
                    <Button className="complete" onClick={this.toComponyInfo}>
                        提交注册
                    </Button>
                </div>
            </div>
        );
    }
}

export default ValiAccount;
