import React, {Component} from "react"
import { Link } from "react-router-dom";
import { withRouter } from 'react-router'
import {Icon } from 'antd';
import './index.scss'
import {IconUrl} from "@/config.js"
const IconFont = Icon.createFromIconfontCN({
  scriptUrl: IconUrl,
});
class InsideHeader extends Component{
  constructor(props){
    super(props)
    this.state={
      bannerList:[
        {path:'/home',name:'首页'},
        {path:'/inquiry',name:'发布询价'},
        {path:'/offer',name:'查看报价'},
        {path:'/shop',name:'购物车'},
        {path:'/orderManage',name:'订单跟踪'}
      ],
      selectKey:0
    }
  }
  selectBanner(path,e){
    let {history}=this.props
    history.push({
      pathname: path,
    })
  }
  render(){
    let {bannerList,selectKey}=this.state
    let {pathname}=this.props.location
    return (
      <header className="inside-header">
        <div className="sign-out">
          <div className="container">
            <span>广东好快省汽车服务有限公司</span>
            <button className="sign-out-btn">退出登录</button>
          </div>
        </div>
        <div className="container header-banner clearfix">
          <div className="banner-left">
            <span className="logo"></span>
            <h4 className="title">汽配商城</h4>
          </div>
          <nav className="banner-right">
            {
              bannerList.map((item,index)=>{
                return (
                  <div className={pathname.includes(item.path)?'service-item selected':'service-item'} key={index} onClick={this.selectBanner.bind(this,item.path)}>
                    <Link to={item.path} >{item.name}</Link>
                    <span className="banner-icon"><i className="banner_hover"></i></span>
                  </div>
                )
              })
            }
          </nav>
        </div>
      </header>
    )
  }
}

export default withRouter(InsideHeader)