import React, {Component} from 'react'
import { Checkbox,Radio,Icon  } from 'antd';
import {IconUrl} from "@/config.js"
import './index.scss'
import {actionParts,allRetract,filterShow} from '../../actions'
import {connect} from 'react-redux';
const CheckboxGroup = Checkbox.Group;
const IconFont = Icon.createFromIconfontCN({
  scriptUrl: IconUrl,
});
const plainOptions = ['原厂原包', '原厂配套', '拆车件', '再制造件', '品牌']
class PartsList extends Component{
  constructor(props){
    super(props)
    this.state={
      allFlag:true, //默认全部打开
      selectedQuality:[],
      isInvoice:false
    }
    this._allRetract=this._allRetract.bind(this)
    this.changeQuality=this.changeQuality.bind(this)
    this.changeInvoice=this.changeInvoice.bind(this)
  }
  retractSingle(nameKey,oeKey){
    let {dispatch} = this.props
    dispatch(actionParts(nameKey,oeKey))
  }
  changeRadio(nameKey,oeKey,goodsKey){
    let {dispatch} = this.props
    dispatch(actionParts(nameKey,oeKey,goodsKey))
  }
  _allRetract(){
    let {dispatch} = this.props
    let {allFlag} = this.state
    allFlag=!allFlag
    dispatch(allRetract(allFlag))
    this.setState({
      allFlag
    })
  }
  //配件品质改变
  changeQuality(list){
    let {dispatch} = this.props
    let {isInvoice}=this.state;
    this.setState({
      selectedQuality:list,
      allFlag:true
    })
    dispatch(filterShow(list,isInvoice))
  }
  //改变发票
  changeInvoice(e){
    let {dispatch} = this.props
    let {selectedQuality}=this.state;
    let isInvoice=e.target.checked
    this.setState({
      isInvoice,
      allFlag:true
    })
    dispatch(filterShow(selectedQuality,isInvoice))
  }
  render(){
    let {offerList} = this.props
    let {allFlag} =this.state
    return(
      <React.Fragment>
        <div className="parts-quality">
          <span className="quality-check">配件品质:</span>
          <CheckboxGroup options={plainOptions} onChange={this.changeQuality} />
          <span className="invoice">发票要求:</span>
          <Checkbox onChange={this.changeInvoice}>需要发票</Checkbox>
          <button className="retract" onClick={this._allRetract}>{allFlag?'收起全部报价':'展开全部报价'}</button>
        </div>
        <ul className="parts-wrap">
          {
            offerList.map((item,nameKey)=>(
              <li id={item.hrefId} className="parts-single" key={item.nameKey}>
                <p className="single-info">
                  <span className="single-order">{nameKey+1}</span>
                  <span className="number">{item.PartsName}</span>
                  <span className="num">x{item.PartsNameCount}</span>
                </p>
                {
                  item.OEList.length?item.OEList.map((oe,oeKey)=>(
                    oe.DetailList.length?
                    <React.Fragment key={oeKey}>
                      <p className="origin-parts">
                        <span className="origin-number">原厂零件号：{oe.OECode}</span>
                        <span className="origin-price">4S参考价：￥{oe.ReferencePrice}</span>
                        <span className="drow-up" onClick={this.retractSingle.bind(this,nameKey,oeKey)}>{oe.retractFlag?'收起报价':'展开报价'}<IconFont type="icon-xiala" className={oe.retractFlag?'retract-down':'retract-up'}></IconFont></span>
                      </p>
                      <ol className={oe.retractFlag?'conmmen-name':'oe-none'}>
                      {
                        oe.DetailList.map((goods,goodsKey)=>(
                          <li className="parts-item" key={goodsKey}>
                            <Radio checked={goods.radioFlag} onChange={this.changeRadio.bind(this,nameKey,oeKey,goodsKey)}></Radio>
                            <span className="parts-price">￥{goods.PartsPrice}</span>
                            <span className="brand">{goods.PartsBrand} </span>
                            <span className="store-info">{goods.PartsPlace} 库存 {goods.PartsCount}</span>
                            <div className="parts-tip">
                              {goods.invoiceDesc?(<span>{goods.invoiceDesc}</span>):''}
                              {goods.freightDesc?(<span>{goods.freightDesc}</span>):''}
                              {goods.DeliveryTime?(<span>{goods.DeliveryTime}内到货</span>):''}
                              {goods.Warrantyperiod?(<span>质保{goods.Warrantyperiod}个月</span>):''}
                            </div>
                            <span className="address">{goods.SupplierName}</span>
                            {/* <IconFont className="msg" type="icon-message"></IconFont>
                            <IconFont className="phone" type="icon-dianhua"></IconFont> */}
                          </li>
                        ))
                      }
                      </ol>
                    </React.Fragment>:null
                  )):(<div className="none-price">暂无报价</div>)
                }
              </li>
            ))
          }
        </ul>
      </React.Fragment>
    )
  }
}
export default connect()(PartsList)