import React, { Component } from "react";
import { Form, Icon, Input, Button, Checkbox, Row, Col } from "antd";
import { connect } from "react-redux";
import { getUserInfo } from "../actions";
import {login}from '../api'
import "./login.scss";

@connect(
    state => state.Login_reducer,
    { getUserInfo }
)
class LoginForm extends Component {
    // constructor(props) {
    //     super(props);
      
    // }
    handleSubmit = e => {
        e.preventDefault();

 
        
        this.props.form.validateFields((err, values) => {
          if (!err) {
              console.log(values);
              
            const{accountName,accountPassword,remember} = values
            const {history} = this.props
            const data = {customerAccount:{accountName,accountPassword}}
            if(remember){
                localStorage.setItem('__account__',accountName)
            }else{
                localStorage.removeItem('__account__',accountName)
            }
            login(data).then(res => {
                if (res.data.errcode == 0) {
                   this.props.getUserInfo({accountName}).then(success=>{
                       if(success){
                        history.push('/home')
                       }
                   })
                }
            });

          }
        });
    };
    componentDidMount() {
        const account = localStorage.getItem('__account__')
        if(account){
            this.props.form.setFieldsValue({accountName:account})
        }
     
    }
    handleChange=e=>{
        const value = e.target.value.trim()
        const name = e.target.name
        this.setState({
            [name]:value
        })
    }
    render() {
        const { getFieldDecorator } = this.props.form;
        return (
            <div className="login-wrap">
                <Row>
                    <Col span={16} push={4} className="login">
                        <Form
                            onSubmit={this.handleSubmit}
                            className="login-form"
                        >
                            <Form.Item>
                                {getFieldDecorator("accountName", {
                                    rules: [
                                        {
                                            required: true,
                                            message: "请输入用户名!"
                                        }
                                    ]
                                })(
                                    <Input
                                    name = 'accountName'
                                        prefix={
                                            <Icon
                                                type="user"
                                                style={{
                                                    color: "rgba(0,0,0,.25)"
                                                }}
                                            />
                                        }
                                        placeholder="请输入账号"
                                    />
                                )}
                            </Form.Item>
                            <Form.Item>
                                {getFieldDecorator("accountPassword", {
                                    rules: [
                                        {
                                            required: true,
                                            message: "请输入密码!"
                                        }
                                    ]
                                })(
                                    <Input
                                    name='accountPassword'
                                        prefix={
                                            <Icon
                                                type="lock"
                                                style={{
                                                    color: "rgba(0,0,0,.25)"
                                                }}
                                            />
                                        }
                                        type="password"
                                        placeholder="请输入密码"
                                    />
                                )}
                            </Form.Item>
                            <Form.Item className="btn-wrap">
                                {getFieldDecorator("remember", {
                                    valuePropName: "checked",
                                    initialValue: true
                                })(<Checkbox>记住账号</Checkbox>)}
                                <span className="login-form-forgot">
                                    忘记密码
                                </span>
                                <Button
                                    type="primary"
                                    htmlType="submit"
                                    className="login-form-button"
                                >
                                    登录
                                </Button>
                                <button className="login-form-register">
                                    还没有账号? <span onClick={()=>{this.props.history.push('/register')}}>去注册</span>
                                </button>
                            </Form.Item>
                        </Form>
                    </Col>
                </Row>
            </div>
        );
    }
}
const Login = Form.create({ name: "normal_login" })(LoginForm);
export default Login;
