import React, { Component } from "react";
import { Checkbox } from "antd";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { orderUpdate } from "cont/order/api";

@withRouter
@connect(state => ({
    stateMap: state.OrderManage_reducer.stateMap,
    stateBtntextMap: state.OrderManage_reducer.stateBtntextMap
}))
class OrderItem extends Component {
    // constructor(props){
    //     super(props)
    // }

    toOrderDetail = orderNum => {
        this.props.history.push(`/orderDetail/${orderNum}`);
    };
    toPay = state => {
        const { confirmCallback, orderInfo } = this.props;
        const { purchaseorder } = orderInfo;
        const { purchaseOrderNumber } = purchaseorder;
        const waitPay = 1,
            waitConfirm = 4;
        if ((this.state = waitPay)) {
            //去支付
            this.props.history.push(`/cashier/${purchaseOrderNumber}`);
        } else {
            //去确认收货 TODO:
            const data = {
                purchaseOrder: {
                    purchaseOrderNumber,
                    purchaseOrderState: 1,
                    ConsigneeState: 1, //收发货状态，0-未收货，1-已收货，2-部分收货，3-未发货，4-已发货，5-部分发货
                    ConfirmedState: 1, //确认状态，0-否，1-是
                    ServiceState: 2 //服务状态，0-未使用，1-施工中，2-服务完成；
                },
                purchaseorderattribute: {
                    IsScore: 0 //是否评分标志，0-否，1-是
                }
            };

            orderUpdate(data).then(res => {
                //确认收货后更新列表
                if (res.data.errcode == 0) {
                    confirmCallback();
                }
            });
        }
    };
    render() {
        const { stateMap, stateBtntextMap } = this.props;
        const { purchaseorder, parttopurchases } = this.props.orderInfo;
        const items = parttopurchases.map((v, i) => (
            <div key={i}>
                <span className="part">
                    {v.partName} {v.oecode ? `| ${v.oecode}` : ""}
                </span>
                <span className="price">￥{v.priceReal || v.unitPrice}</span>
                <span className="count">
                    {v.partQuantity || v.applyQuantity}
                </span>
            </div>
        ));
        return (
            <div className="orderItem box-hover">
                <div className="item-header">
                    <Checkbox className="mr15" />
                    <span className="mr20">
                        {purchaseorder.createTime || purchaseorder.applyTime}
                    </span>
                    <span className="mr20">
                        订单编号：
                        {purchaseorder.purchaseOrderNumber ||
                            purchaseorder.returnGoodsNumber}
                    </span>
                    <span className="mr20">
                        供应商：{purchaseorder.supplieName}
                    </span>
                    <span className="mr20">
                        备注：{purchaseorder.purchaseDescribe}
                    </span>
                </div>
                <div className="item-con">
                    <div className="item-partInfo">{items}</div>
                    <div className="realAmount">
                        ￥
                        {purchaseorder.purchaseAmount ||
                            purchaseorder.applyRefundAmount}
                    </div>
                    <div className="state">
                        <span
                            className="toPay"
                            style={{ marginBottom: "10px" }}
                        >
                            {stateMap[purchaseorder.state]}
                            {/* 等待付款 */}
                        </span>
                        <span  onClick={() => {
                                this.toOrderDetail(
                                    purchaseorder.purchaseOrderNumber
                                );
                            }}>订单详情</span>
                    </div>
                    <div className="operation">
                        {stateBtntextMap[purchaseorder.state] ? (
                            <button
                                className="toPay"
                                onClick={() => {
                                    this.toPay(purchaseorder.state);
                                }}
                            >
                                {stateBtntextMap[purchaseorder.state]}
                            </button>
                        ) : null}

                        <span
                            style={{ margin: "10px 0" }}
                            onClick={() => {
                                this.toOrderDetail(
                                    purchaseorder.purchaseOrderNumber
                                );
                            }}
                        >
                            订单详情
                        </span>
                        <span
                            onClick={() => {
                                this.toOrderDetail(
                                    purchaseorder.purchaseOrderNumber
                                );
                            }}
                        >
                            查看物流
                        </span>
                    </div>
                </div>
            </div>
        );
    }
}

export default OrderItem;
