/*type
 1:首字母大写
 2：首页母小写
 3：大小写转换
 4：全部大写
 5：全部小写
 * */
//ecDo.changeCase('asdasd',1)
//result：Asdasd
export const changeCase = (str, type) => {
    function ToggleCase(str) {
        var itemText = "";
        str.split("").forEach(function(item) {
            if (/^([a-z]+)/.test(item)) {
                itemText += item.toUpperCase();
            } else if (/^([A-Z]+)/.test(item)) {
                itemText += item.toLowerCase();
            } else {
                itemText += item;
            }
        });
        return itemText;
    }
    switch (type) {
        case 1:
            return str.replace(/\b\w+\b/g, function(word) {
                // .toLowerCase()
                return word.substring(0, 1).toUpperCase() + word.substring(1);
            });
        case 2:
            return str.replace(/\b\w+\b/g, function(word) {
                // .toUpperCase()
                return word.substring(0, 1).toLowerCase() + word.substring(1);
            });
        case 3:
            return ToggleCase(str);
        case 4:
            return str.toUpperCase();
        case 5:
            return str.toLowerCase();
        default:
            return str;
    }
};

export const Cookie = {
    setCookie(name, value, iDay) {
        let Day = iDay || 0;
        var oDate = new Date();
        oDate.setTime(oDate.getTime() + Day * 24 * 60 * 60 * 1000);
        document.cookie = name + "=" + escape(value) + ";expires=" + oDate;
    },
    getCookie(name) {
        var arr,
            reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");
        return (arr = document.cookie.match(reg)) ? unescape(arr[2]) : null;
    },
    delCookie(name) {
        var exp = new Date();
        exp.setTime(exp.getTime() - 1000 * 60);
        var cval = this.getCookie(name);
        if (cval != null) {
            document.cookie =
                name + "=" + null + ";expires=" + exp.toGMTString();
        }
    }
};
export function jsonp(obj) {
    //写入url地址中的函数名称，动态创建
    var callbackName =
        "jsonp_callback_" +
        Date.now() +
        Math.random()
            .toString()
            .substr(2, 5);

    //创建一个script标签
    var scriptObj = document.createElement("script");
    obj.parames = obj.parames || "";
    if (typeof obj.parames == "object") {
        var arr = new Array();
        for (var key in obj.parames) {
            arr.push(key + "=" + obj.parames[key]);
        }
        obj.parames = arr.join("&");
    }
    //设置标签的请求路径
    //像这样：http://localhost:3000/api?callback=jsonp_callback_153498520409635392&id=1
    scriptObj.src =
        obj.url + "?" + "callback=" + callbackName + "&" + obj.parames;
    //将创建好的带请求的script标签添加到html的body中
    document.body.appendChild(scriptObj);
    //这里必须这样写window[callbackName];
    //如果写window.callbackName会报错没有定义
    window[callbackName] = function(res) {
        obj.success(res);
        //删除的时候可以这样写
        //由于请求一次会创建一个script标签和一个函数，所以每次获取到结果后就删除
        delete window.callbackName;
        document.body.removeChild(scriptObj);
    };
}
export function stringNorepeat(arr) {
    var len = arr.length;
    arr.sort();
    for (var i = len - 1; i > 0; i--) {
        if (arr[i] == arr[i - 1]) {
            arr.splice(i, 1);
        }
    }
    return arr;
}

//获取渠道名称 channelCode通过url传过来
export function formatUrl(search) {
    search = search||window.location.search
    var obj = {};
    var params = search?search.slice(1).split("&") : [];
    params.forEach(function(item) {
        var arr = item.split("=");
        obj[arr[0]] = arr[1];
    });
    return obj;
}

export  function debounce(fn,time=500){
    let timer
    return function(){
        let args = arguments,me=this;
            clearTimeout(timer)
            timer = setTimeout(function(){
                fn.apply(me,args)
            },time)
    }
}