import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { Cookie } from "util/util.js";
import { setUserinfo } from "cont/login/actions";

@withRouter
@connect(
    () => ({}),
    { setUserinfo }
)
class AuthRouter extends Component {
    componentDidMount() {
        //不用拦截的路由
        const allowRouters = ['/login','/register','/register/1','/register/2','/userProtocol']
        const pathname = this.props.location.pathname;
        if (allowRouters.indexOf(pathname)>-1) {
            return null;
        }
        //请求信息
        const info = Cookie.getCookie("__hksuserinfo__");
        if (info) {
            this.props.setUserinfo(JSON.parse(info));
        } else {
            this.props.history.push("/login");
        }
    }
    render() {
        return null;
    }
}

export default AuthRouter;
