import React, { Component } from "react";
import  { connect } from "react-redux";

import { Input, Button, Checkbox, message } from "antd";
import { withRouter } from "react-router-dom";
import Tips from "./Tips";
import Slider from "./Slider";
import { sendCode, checkCode } from "../../api";
import {setMobile} from '../../actions'
// 验证手机
@withRouter
@connect(s=>s.Register_reducer,{setMobile})
class ValiPhone extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isRegister: null,
            isCompleted: false,
            mobile: sessionStorage.getItem("__mobile")
                ? sessionStorage.getItem("__mobile")
                : "", //手机号码
            code: "", //验证码
            error: "",
            flag: sessionStorage.getItem("__flag__") == "true" ? true : false, //true表示已经发送验证码了
            time: sessionStorage.getItem("__time__")
                ? sessionStorage.getItem("__time__")
                : "" ,//重发剩余的时间
            isAgreeProtocel:true,//是否同意用户协议
        };
    }
    componentWillMount(){
        if (this.state.flag) {
            let timer = setInterval(() => {
                console.log(this.state.time);
                if (this.state.time <= 0) {
                    this.setState({
                        flag:false
                    })
                    sessionStorage.setItem("__flag__", false);
                    clearInterval(timer);
                    // sessionStorage.setItem("__flag__", false);
                    return;
                }
                this.setState({
                    time: this.state.time - 1
                });
                // this.time--;
                sessionStorage.setItem("__time__", this.state.time);
            }, 1000);
        }
    }
    //滑块移动
    sliderMoveChange = isCompleted => {
        this.setState({ isCompleted });
    };
    vali = () => {
        const { mobile, isCompleted,code ,isAgreeProtocel} = this.state;
        if (!/^1[0-9]{10}$/.test(mobile)) {
            this.setState({ error: true });
            return false;
        } else {
            this.setState({ error: false });
        }
        if (!isCompleted) {
            message.error("请滑动验证！");
            return false;
        }
        if (!code) {
            message.error("验证码不能为空！");
            return false;
        }
        if(!isAgreeProtocel){
            message.error("请阅读并同意好快省用户协议！");
            return false;
        }
        return true;
    };
    //获取验证码
    getCode = () => {
        if(this.flag){
            return
        }
        const { mobile } = this.state;
        if (!mobile) {
            message.info('请输入手机号码！')
            return;
        }
        if (!/^1[0-9]{10}$/.test(mobile)) {
            message.info('请输正确的手机号码！')
            return 
        }

        const data = {
            type: 2,
            phoneNumber: mobile
        };
        if (!this.flag || (this.flag && this.state.time == 0)) {
            sendCode(data).then(res => {
                if (res.data.errcode == 0) {
                    if (res.data.code == 0) {
                        message.warn("该手机号已绑定账号！");
                        this.flag = false;
                        this.setState({
                            flag: false
                        });
                        sessionStorage.setItem("__flag__", false);
                    } else if (res.data.code == 1) {
                        //没有账号
                        message.success(res.data.errmsg);
                        //发送成功
                        sessionStorage.setItem(
                            "__mobile",
                            mobile
                        );
                        this.setState({
                            flag: true,
                            time: 120
                        });
                        sessionStorage.setItem("__flag__", true);
                        let timer = setInterval(() => {
                            console.log(this.state.time);
                            
                            if (this.state.time<= 0) {
                                this.setState({
                                    flag:false
                                })
                                clearInterval(timer);
                                sessionStorage.setItem("__flag__", false);
                                return;
                            }
                            this.setState({
                                time: this.state.time - 1
                            });
                            sessionStorage.setItem("__time__", this.state.time);
                        }, 1000);
                    } else {
                        this.setState({
                            flag: false
                        });
                        sessionStorage.setItem("__flag__", false);
                    }
                }
            });
        }
    };

    handleChange = e => {
        const value = e.target.value.trim(),
            name = e.target.name;
        this.setState({ [name]: value });
        if (name == "mobile") {
            if (!value) {
                this.setState({ error: true });
            } else if (!/^1[0-9]{10}$/.test(value)) {
                this.setState({ error: true });
            } else {
                this.setState({ error: false });
            }
        }
    };
    toAccountInfo = () => {

        const {setMobile} = this.props
        const isVali = this.vali()
        console.log('********************');
        console.log(isVali);
        console.log('********************');
        
        
        if (!isVali) {
            return;
        }
        const { mobile, code } = this.state;
        
        // const data = {
        //     code,
        //     phoneNumber: mobile
        // };
        // checkCode(data).then(res => {
        //     if (res.data.errcode != 0) {
        //         message.error("验证码输入有误");
        //         return;
        //     }
        //     setMobile(mobile)
        //     this.props.history.push("/register/2");
        // });
       setMobile(mobile)
       this.props.history.push("/register/2");
    };
    checkChange=()=>{
        this.setState({
            isAgreeProtocel:!this.state.isAgreeProtocel
        })
    }
    toUserProtocol=()=>{
        this.props.history.push('/userProtocol')
    }
    render() {
        const {  isCompleted, code, mobile, error,flag,time,isAgreeProtocel } = this.state;
        const btnText = flag?  `重发（${time}）s` :'获取验证码'
        // const error = isRegister !== null && isRegister === true;
        // console.log(error);
        return (
            <div className="vali valiPhone">
                <div>
                    <span className="label-input">
                        <i className="mr5">*</i>验证手机
                    </span>
                    <Input
                        className="mr15"
                        value={mobile}
                        name="mobile"
                        onChange={this.handleChange}
                    />
                    <Tips error={error} errMsg="手机号码格式不正确" />
                </div>
                <div className="vAlignMiddle mb15">
                    <span className="label-input">
                        <i className="mr5">*</i>滑动验证
                    </span>
                    <Slider
                        moveChange={this.sliderMoveChange}
                        isCompleted={isCompleted}
                    />
                    {/* <Input /> */}
                </div>
                <div>
                    <span className="label-input">
                        <i className="mr5">*</i>短信验证码
                    </span>
                    <Input
                        className="code"
                        name="code"
                        value={code}
                        onChange={this.handleChange}
                    />
                    <Button className="getcode" onClick={this.getCode}>
                        {btnText}
                    </Button>
                    {/* <span>
                        收不到验证码？<span className="link">点击此处</span>
                        获取语音验证码
                    </span> */}
                </div>
                <div className="valiphone-item">
                    <Checkbox className="mr8" name="isAgreeProtocel" checked={isAgreeProtocel} onChange = {this.checkChange}/>
                    已阅读并遵守<span className="warn" onClick={this.toUserProtocol}>《好快省用户协议》</span>
                </div>
                <div className="valiphone-item">
                    <Button className="complete" onClick={this.toAccountInfo}>
                        完成验证
                    </Button>
                </div>
            </div>
        );
    }
}

export default ValiPhone;
