import React, { Component } from "react";
import PropTypes from "prop-types";
import "./index.scss";

class Count extends Component {
    static defaultProps = {
        num:99
    }

    onChange=(e)=>{
        const num = e.target.value
        this.props.onChange(num)
    }
    reduceNum=()=>{
        const {num}  = this.props
        if(num<=0){
            return
        }
        
        this.props.onChange(num-1)
    }
    addNum=()=>{
        const {num}  = this.props
        if(num>=99){
            return
        }
        this.props.onChange(num+1)
    }
    render() {
        const {num}  = this.props
        return (
            <span className="count-com">
                <span className="reduce" onClick={this.reduceNum}>-</span>
                <input type="number" className="num" value={num} onChange={this.onChange}/>
                <span className="add" onClick={this.addNum}>+</span>
            </span>
        );
    }
}
Count.propTypes = {
    num: PropTypes.number.isRequired,
    onChange: PropTypes.func.isRequired
};

export default Count;
