import React, { Component } from "react";
import "./slider.scss";
import { Icon } from "antd";
import PropTypes from "prop-types";
// 验证滑动验证
class Slider extends Component {
    static defaultProps = {
        width: 280
    };
    constructor(props) {
        super(props);
        this.state = {
            // isCompleted: false,
            btnLeft: 0
        };
        this.canMove = true; //移动最大位置后不可再移动
        this.isDown = false;
        this.popRef = React.createRef();
        this.btnRef = React.createRef();
    }
    componentDidMount() {
        // console.log(this.popRef);

        var rect = this.popRef.current.getBoundingClientRect();
        // console.log(rect);
        this.startX = rect.left;
        document.addEventListener('mouseup',()=>{
            if(this.isDown = true){
                this.isDown = false
            }
        })
    }
    handleMove = e => {
        if (this.isDown && this.canMove) {
            // console.log(e.clientX);
            console.log(e.clientX);

            let left = e.clientX - this.startX;
            
            if (left > 236) {
                left = 236;
                this.canMove = false;
                const {moveChange} = this.props
                moveChange(true)
                document.removeEventListener('mousemove',this.handleMove)
                // this.setState({
                //     isCompleted:true
                // })
            }else if(left<0){
                left = 0
            }

            this.setState({
                left
            });
            // this.btnRef.current.style.left = left+'px'
        }
    };

    handleDown = e => {
        this.isDown = true;
        document.addEventListener('mousemove',this.handleMove)
    };
    handleUp = e => {
        this.isDown = false;
        let { left } = this.state;

        // if (left < 230) {
        //     this.setState({
        //         left: 0
        //     });
        // } else
         if (left > 236) {
            this.setState({
                left: 236
            });
        }
        
    };
    render() {
        const {  left } = this.state;
        const { isCompleted } = this.props;

        let btnContent = isCompleted ? (
            <Icon
            type="check-circle"
            style={{ color: "#4dc43d" }}
            theme="filled"
        /> 
        ) : (
            ">>"
        );
        let text = isCompleted ? "通过验证" : "";

        const styObj = { width: this.props.width };

        const btnStyle = { left };
        const movingWidth = { width: left };
        return (
            <div className="slider" style={styObj}>
                <div className="pop" style={styObj} ref={this.popRef}>
                    请将滑块移动至最右侧
                </div>
                <div className="moving" style={movingWidth}>
                    {text}
                </div>
                <button
                className={isCompleted?'completed':''}
                    style={btnStyle}
                    ref={this.btnRef}
                    // onMouseMove={this.handleMove}
                    onMouseUp={this.handleUp}
                    onMouseDown={this.handleDown}
                >
                    {btnContent}
                </button>
            </div>
        );
    }
}
Slider.propTypes = {
    width: PropTypes.number.isRequired
};

export default Slider;
