import { handleActions } from "redux-actions";
import * as TYPES from "./actionTypes";
import { Cookie } from "util/util.js";
//处理购物车列表
export const login = handleActions(
    {
        [TYPES.SET_USERINFO](state, action) {
            //获取购物车列表 传递购物车列表
            const hks_userInfo = action.payload;
            return {...state,hks_userInfo};
        },

    },
    {
        hks_userInfo:Cookie.getCookie("__hksuserinfo__")?JSON.parse(Cookie.getCookie("__hksuserinfo__")):{},

    }
);

export default login;
