import { createAction } from "redux-actions";
import * as SHOP from './api.js'
import * as TYPES from "./actionTypes";

export const shopList = createAction(TYPES.SHOP_LIST); //购物车列表
export const changeFirstCheck = createAction(TYPES.CHANGE_FIRST_CHECKED); //一级选中修改
export const changeSecondCheck = createAction(TYPES.CHANGE_SECOND_CHECKED); //二级选中修改
export const changeAllCheck = createAction(TYPES.CHANGE_ALL_CHECKED); //全选修改
export const changeNum = createAction(TYPES.CHANGE_NUM); //修改数量
export const getCountAndPrice = createAction(TYPES.GET_CHECKEDCOUNT); //修改下面的选中数量和价格
const getTotalInventory = (inventory)=> {
    let storeNum = 0;
    if (inventory && inventory.length) {
        inventory.forEach(item => {
            storeNum += item.partQuantity;
        });
    }
    return storeNum;
}
