//删除购物车
export const delShopGoods = (delQuery,getQuery,allChecked)=>dispatch=>{
    return SHOP.delShopList(delQuery).then(res=>{
        if(res.data.errcode==0){
            dispatch(getShopList(getQuery,allChecked))
            //重新获取shoplist
        }
    })
}
//更新购物车
export const updateShopGoods = (updateQuery,getQuery)=>dispatch=>{
    return SHOP.updateShopList(updateQuery).then(res=>{
        if(res.data.errcode==0){
            // dispatch(getShopList(getQuery))
            //重新获取shoplist
        }
    })
}



