import React, { Component } from "react";
import { Radio } from "antd";
import { connect } from "react-redux";

import { getPayList, getDetail } from "../actions";
import "./index.scss";
import { appCode } from "config/config";
import  {apiMaps,env} from 'config/config'
@connect(
    state => ({
        ...state.Cashier_reducer,
        userinfo: state.Login_reducer.hks_userInfo
    }),
    { getPayList, getDetail }
)
class Cashier extends Component {
    constructor(props) {
        super(props);
        this.state = {
            payVal: 1,//支付方式的值
        };
    }
    handleChange = e => {
        this.setState({
            payVal: e.target.value
        });
    };
    componentDidMount() {
        const { getPayList, getDetail } = this.props;
        const { order } = this.props.match.params; //获取订单号
        getDetail({ purchaseOrderNumber: order }); //获取订单详情
        getPayList();
    }
    toPay = () => {
        const { userinfo, match, orderDetail ,purchaseAmount} = this.props;
        const { payVal} = this.state;
        let orderDescribe;
        if (orderDetail.parttopurchase && orderDetail.parttopurchase.length) {
            orderDescribe = orderDetail.parttopurchase
                .map(v => {
                    return `${v.partName}:数量${v.partQuantity}`;
                })
                .join(";");
        }else{
            orderDescribe='没有配件'
        }

        const query = {
            appCode,
            originCode: match.params.order,
            orderTitle: "好快省订单",
            orderDescribe,
            paymentAmount: 1,//purchaseAmount,
            userID: userinfo.customer.customerID,
            userName: userinfo.customer.customerName,
            backUrl: "http://192.168.18.145:3000/paySuccess",
            paymentID: payVal,
            notifyUrl:'http://192.168.18.145:3001/listen'
        };
        // let url = `${apiMaps['pay'][env]}unifyPay/payIndex.html?`;

        let url = "http://fengtaijun.ngrok.wendal.cn/unifyPay/payIndex.html?";

        Object.keys(query).forEach(key => {
            url += `${key}=${query[key]}&`;
        });
        window.location.href = url;
    };
    render() {
        const { payVal } = this.state;
        const { payList, orderDetail } = this.props;
        const {
            createTime,
            purchaseOrderNumber,
            purchaseAmount
        } = orderDetail.purchaseorder;
        const RadioGroup = Radio.Group;
        console.log("^^^^^^^^^^^^^^^^^^^^^^^^^");
        console.log(
            this.props.orderDetail,
            purchaseOrderNumber,
            createTime,
            purchaseAmount
        );
        console.log("^^^^^^^^^^^^^^^^^^^^^^^^^");
        const payItems = payList.map(v => {
            const item = v.status ? (
                <div className="pay-item" key={v.paymentID}>
                    <Radio value={v.paymentID} />
                    <img src="" alt="" />
                    <span>{v.paymentName}</span>
                </div>
            ) : null;
            return item;
        });
        return (
            <div className="cashier container">
                <div className="cashier-header">
                    <h3>
                        <i />
                        收银台
                    </h3>
                </div>
                <div className="cashier-con">
                    <div className="con-header">
                        <span>采购单号：</span>
                        <span>{purchaseOrderNumber}</span>
                        <span className="ml20">下单时间：</span>
                        <span>{createTime}</span>
                        <span className="fr">
                            应付总额
                            <strong className="warn">￥{purchaseAmount}</strong>
                        </span>
                    </div>
                    <RadioGroup onChange={this.handleChange} value={payVal}>
                        {/* <div className="pay-item">
                            <Radio value={1} />
                            <img src="" alt="" />
                            <span>微信支付</span>
                        </div>
                        <div className="pay-item">
                            <Radio value={2} />
                            <img src="" alt="" />
                            <span>微信支付</span>
                        </div>
                        <div className="pay-item">
                            <Radio value={3} />
                            <img src="" alt="" />
                            <span>白条支付</span>
                            <span className="fr credit">
                                可用余额 1500.00元
                            </span>
                        </div> */}
                        {payItems}
                    </RadioGroup>

                    <div className="pay">
                        <button className=" " onClick={this.toPay}>
                            立即付款
                        </button>
                    </div>
                </div>
            </div>
        );
    }
}

export default Cashier;
