import React, { Component } from 'react';
import { Button,Icon } from 'antd';
import {connect} from 'react-redux';
import {inqueryDetail,addShopCar} from "../actions.js"
import OfferRequire from "./detailLeft"
import PartsRight from "./partsRight"
import './offerDetail.scss'
import {IconUrl} from "@/config.js"
const IconFont = Icon.createFromIconfontCN({
  scriptUrl: IconUrl,
});
class OfferDetail extends Component{
  constructor(props){
    super(props)
  }
  componentDidMount(){
    let {getDetail,match} =this.props
    getDetail({Inquiryno:match.params.id})
  }
  addShopcar=()=>{
    let {addShop} =this.props
    addShop()
  }
  render(){
    let {offerInfo,showOffer} = this.props
    let leftList=[]
    showOffer.forEach(name=>{
      let num=0
      name.OEList.forEach(oe=>{
        num+=oe.DetailList.length
      })
      leftList.push({
        hrefId:'#'+name.hrefId,
        name:name.PartsName,
        num:num
      })
    })
    return(
      <React.Fragment>
        <div className="container offer-detail">
          <aside className="left-aside">
            <OfferRequire offerInfo={offerInfo} leftList={leftList}></OfferRequire>
          </aside>
          <div className="detail-info">
            {
              showOffer.length?(
                <PartsRight offerList={showOffer} ></PartsRight>
              ):(
                // <IconFont type="icon-zanwushuju" style={{ fontSize: '16px', color: '#08c' }}  className="none-data">1</IconFont>
                <div className="none-data">
                  暂无数据
                </div>
              )
            }
          </div>
        </div>
        {
          showOffer.length?(
            <div className="add-shop">
                <div className="container">
                  <Button type="primary" onClick={this.addShopcar}>加入购物车</Button>
                </div>
            </div>
          ):null
        }
        
      </React.Fragment>
    )
  }

}
const mapStateToProps=(state) =>{
  let data=state.offerDetail_reducer
  return ({
    offerInfo:data.offerInfo,
    showOffer:data.showOffer
  })
}
const mapDispatchToProps=(dispatch, ownProps)=>{
  return({
    addShop:()=>dispatch(addShopCar()),
    getDetail:(params)=>dispatch(inqueryDetail(params))
  })
}
export default connect(mapStateToProps,mapDispatchToProps)(OfferDetail)