import React, { Component } from "react";
import { Checkbox } from "antd";
import PartItem from "./PartItem";
class InquiryItem extends Component {
   

    constructor(props) {
        super(props);
        this.state = {
            inpuiryChecked: false, //询价是否选中
            // partInfo: [
            //     {
            //         id: 1,
            //         partName: "机油滤清器",
            //         oe: 16127233840,
            //         support: "广东好快省汽车服务有限公司",
            //         price: 150,
            //         num: 1,
            //         unit: "个",
            //         origin: "陆地卫士",
            //         ArriveTime: 24,
            //         quality: "三个月",
            //         freight: "到付",
            //         checked: false
            //     },
            //     {
            //         id: 2,
            //         partName: "机油滤清器",
            //         oe: 16127233840,
            //         support: "广东好快省汽车服务有限公司",
            //         price: 150.00,
            //         num: 1,
            //         unit: "个",
            //         origin: "陆地卫士",
            //         ArriveTime: 24,
            //         quality: "三个月",
            //         freight: "到付",
            //         checked: false
            //     }
            // ]
        };
    }
    componentWillReceiveProps(nexrProps){
        const {checked} = nexrProps.inquiryInfo;
        //当父组件全选时
        this.setState({
            inpuiryChecked:checked
        })
    }
    handleInquiryChange = e => {
        const checked =  e.target.checked
        const {firstIndex,handleChangeForInquiry} = this.props
        handleChangeForInquiry(firstIndex,checked)
       
    };
   
   
    render() {
        const { inpuiryChecked} = this.state;
        const {firstIndex,inquiryInfo,handleChangeForPart,handChangeNum,delShopGoods,queryData,updateShopGoods,checkAll,customerAccountID} = this.props;
        const {partItems,vehicle,inquiryNumber} = inquiryInfo
        const itmes = partItems.map((obj, i) => {
            return (
                <PartItem
                customerAccountID={customerAccountID}
                    key={i}
                    partInfo={obj}
                    vehicle={vehicle}
                    firstIndex={firstIndex}
                    secondIndex = {i}
                    handleChangeForPart={ handleChangeForPart}
                    handChangeNum={ handChangeNum}
                    delShopGoods={delShopGoods}
                    updateShopGoods={updateShopGoods}
                    queryData={queryData}
                    checkAll={checkAll}
                />
            );
        });
        return (
            <div className="inquiryItem">
                <div className="header">
                    <Checkbox
                        checked={inpuiryChecked}
                        onChange={this.handleInquiryChange}
                    />
                    <span>询价单号：{inquiryNumber}</span>
                    <span>
                        车型：{vehicle&&vehicle.modelBrand} {vehicle&&vehicle.salesName}
                    </span>
                </div>
                <div>{itmes}</div>
            </div>
        );
    }
}

export default InquiryItem;
