import { axios } from '@/config/api';
//获取询价单数量
export const inquiryDetail = (params={}) => {
  return axios({
      url: '/appdata/InquiryQuotation.aspx',
      method: 'get',
      thirdInterface:true,
      params
  }).then(res => res, err => err)
}
//获取汽车品牌logo
export const getLogo = (data) => {
  return axios({
      url: 'auth/epcsModelBrand/queryModelbrand',
      method: 'post',
      data
  }).then(res => res, err => err)
}
//获取汽车品牌logo
export const addShop = (data) => {
  return axios({
      url: 'auth/shoppingcart/add',
      method: 'post',
      thirdInterface:false,
      data
  }).then(res => res, err => err)
}