import React from 'react'

// 字母排序
function Character(props){
  console.log(props)
  let {character,characterIndex,brandList,selectCharacter,selectBrand} = props
  let showBrandList=[]
  let data=brandList.filter(item=>{
      return item.letter === characterIndex
    })
    if(data.length&&data[0].data.length){
      showBrandList=data[0].data
    }
  return(
    <React.Fragment>
      <div className="letter-wrap">
        {
          character.map((item,index)=>{
            return <span onClick={selectCharacter.bind(this,item)} className={characterIndex===item?'letter-item letter-active':'letter-item'} key={item}>{item}</span>
          })
        }
      </div>
      <ul className="img-wrap">
      {
        showBrandList.length?
        showBrandList.map(item=>{
            return (
              <li className="cars-img" key={item.modelbrandid} onClick={selectBrand.bind(this,item)}>
                <img src={item.modelbrandlogourl} alt=""/>
                <span>{item.modelbrandname}</span>
              </li>
            )
          }):<div className="none-data">暂无数据....</div>
      }
    </ul>
    </React.Fragment>
  )
}
// 车系
function VehicleSystem(props){
  let {vehicleList,selectSeries} = props
  return (
    <ul>
      {
        vehicleList.map((val,index)=>{
          return (
            <li className="made-in" key={index}>
              <p className="made-name"><i></i>{val.makename}</p>
              {
                val.model.map(item=>{
                  return <span className="made-car" onClick={selectSeries.bind(this,item)} key={item.modelid}>{item.model +' '+item.vehicletype}</span>
                })
              }
            </li>
          )
        })
      }
    </ul>
  )
}
// 年份
function CarYears(props){
  let {carYears,selectYear} = props
  return (
    <div className="made-in">
      {
        carYears.map((item,index)=>{
          return <span className="made-car" key={index} onClick={selectYear.bind(this,item)}>{item.yearname}</span>
        })
      }
    </div>
  )
}
// 车型
function CarStyle(props){
  let {styleYearList,selectStyle} = props
  return(
    <div className="style-wrap">
      {
        styleYearList.map(item=>{
          return <span className="car-style" key={item.hkscode} onClick={selectStyle.bind(this,item)}>{item.salesname} {item.productionyear}年产</span>
        })
      }
    </div>
  )
}

export {
  Character,
  VehicleSystem,
  CarYears,
  CarStyle
}