import React, { Component } from 'react';
import { AutoComplete, Input,Icon } from 'antd';
import './index.scss'
import {queryPart} from "cont/inquiry/api.js"
import {IconUrl} from "@/config.js"
const IconFont = Icon.createFromIconfontCN({
  scriptUrl: IconUrl,
});
class Parts extends Component{
  constructor(props){
    super(props)
    this.state = {
      timer:null,
      dataSource: [],
    }
    this.addList=this.addList.bind(this)
  }
  deleteParts(index){
    let {getPartsList,partsList} =this.props
    // let {partsList} =this.state
    if(index<3){
      partsList.splice(index,1,{
        partsname:'',
        oe:'',
        count:1,
        unit:'',
        remark:'',
        tid:''
      })
    }else{
      partsList.splice(index,1)
    }
    getPartsList(partsList)
    // this.setState({
    //   partsList:partsList
    // })
  }
  addList(){
    let {getPartsList,partsList} =this.props
    let hasNameList=partsList.filter(item=>{
      return item.partsname
    })
    if(hasNameList.length===partsList.length){
      partsList.push({
        partsname:'',
        oe:'',
        count:1,
        unit:'',
        remark:'',
        tid:''
      })
    }
    getPartsList(partsList)
  }
  partsChange(index,value){
    let {timer} = this.state
    clearTimeout(timer);
    let dataSource=[]
    if(value){
      let timers = setTimeout(() => {
          let params={
            partTerminologyName:value
          }
          queryPart(params).then(data=>{
            let partsList=[],partsName=params.partTerminologyName
            if (data.errcode === 0) {
                //根据别名来进行过滤
                function searchName(serach, partsName) {
                    var arr = serach.split(",");
                   return arr.some(val=>{
                       return val.indexOf(partsName)==0
                   })
                }
                /**先按照标准名称，再按照别名 */
                /**  */
                let filterData = {
                    partTerminologyName: [],
                    searchName: []
                };
                data.partTerminology.forEach(item => {
                    if (
                        item.partTerminologyName.indexOf(
                            this.partsName
                        ) == 0
                    ) {
                        filterData.partTerminologyName.push(item);
                    } else if (
                        searchName(item.searchName, partsName)
                    ) {
                        filterData.searchName.push(item);
                    }
                });
                Object.keys(filterData).forEach(key => {
                    filterData[key].sort((a, b) => {
                        return (
                            a.partTerminologyName.length -
                            b.partTerminologyName.length
                        );
                    });
                });
                partsList = filterData.partTerminologyName.concat(
                    filterData.searchName
                );
            }
            if(partsList.length){
              partsList.forEach(item=>{
                dataSource.push(item.partTerminologyName)
              })
            }
            this.setState({
              dataSource:dataSource
            })
        })
      }, 500);
      this.setState({
        timer:timers
      })
    }
    let {getPartsList,partsList} =this.props
    partsList[index].partsname=value
    getPartsList(partsList)
  }
  exceptPartsChange(index,e){
    let {getPartsList,partsList} =this.props
    let key=e.target.dataset.key
    let value=e.target.value
    if(key=='count'){
      if(e.target.value<0){
        value=1
      }
    }
    partsList[index][key]=value
    getPartsList(partsList)
  }
  render(){
    const {dataSource} = this.state;
    let {partsList} =this.props
    return(
      <div className="parts">
        <p className="module-info">
          <span className="module-name">配件信息</span>
          {/* <span className="module-other">配件图册</span> */}
        </p>
        <div className="table-wrap">
          <table className="parts-table">
            <thead>
              <tr className="t-item">
                <th className="order">序号</th>
                <th className="name"><i className="is-required">*</i>配件名称</th>
                <th className="oe">OE号</th>
                <th className="num"><i className="is-required">*</i>数量</th>
                <th className="unit"><i className="is-required">*</i>单位</th>
                <th className="remark">备注</th>
                <th className="action">操作</th>
              </tr>
            </thead>
            <tbody>
             {
               partsList.map((item,index)=>{
                  return (
                    <tr className="t-item" key={index}>
                      <td className="order">{index+1}</td>
                      <td className="name">
                        <AutoComplete
                          dataSource={dataSource}
                          style={{ width: 200 }}
                          placeholder="输入配件名称"
                          value={item.partsname}
                          onBlur={this.addList}
                          onChange={this.partsChange.bind(this,index)}
                        />
                      </td>
                      <td className="oe">
                        <Input value={item.oe} data-key='oe' onChange={this.exceptPartsChange.bind(this,index)}/>
                      </td>
                      <td className="num">
                        <Input data-key='count' type='number' value={item.count} onChange={this.exceptPartsChange.bind(this,index)}/>
                      </td>
                      <td className="unit">
                        <Input data-key='unit'  value={item.unit} onChange={this.exceptPartsChange.bind(this,index)}/>
                      </td>
                      <td className="remark">
                        <Input data-key='remark' placeholder="请输入备注" value={item.remark} onChange={this.exceptPartsChange.bind(this,index)}/>
                      </td>
                      <td className="action" onClick={this.deleteParts.bind(this,index)}><IconFont type="icon-shanchu" className="icon-shanchu"></IconFont></td>
                    </tr>
                  )
                })
             } 
            </tbody>
          </table>
        </div>
      </div>
    )
  }

}

export default Parts