/**
 * 表单验证封装
 */
/**
 * 往该对象添加策略
 */
const valiStrategies = {
    required: function(rule) {
        
        var val = rule.val;
        // debugger
        if (val == undefined || val === "" || val === false) {
            return rule.msg;
        }
    },
  
};

class ValidateHks{
    constructor(){
        this.valiArr = [];
    }
    //添加验证规则可以为数组或者对象{msg,val,type,valid}msg 提示信息；val 要验证的值；type 验证类型；valid 自定义验证规则 返回true代表验证通过，false验证失败
    add(options){
        if (Object.prototype.toString.call(options) == "[object Object]") {
            options = [options];
        }
        if (Object.prototype.toString.call(options) != "[object Array]") {
            console.warn("add方法参数为对象或者数组！");
            return;
        }
        for (var i = 0; i < options.length; i++) {
            var rule = options[i];
            var valiFn = (function(rule) {
                return function() {
                    //自定义验证
                    if(rule.valid&&!rule.valid()){
                            return rule.msg;
                    }else if(rule.type){
                    //从验证库里验证
    
                        return valiStrategies[rule.type](rule);
                    }
                };
            })(rule);
            this.valiArr.push(valiFn);
        }
    }
    vali(){
        for (var i = 0, valiFn; (valiFn = this.valiArr[i++]); ) {
            var msg = valiFn();
            if (msg) {
                return msg;
            }
        }
    }
}
