import {createAction} from 'redux-actions'
import * as TYPES from './actionTypes'
import * as ORDER from './api'


export  const addressList = createAction(TYPES.ADDRESS_LIST)//增加地址
export  const addAddress = createAction(TYPES.ADD_ADDRESS)//增加地址
export  const delAddress = createAction(TYPES.DELETE_ADDRESS)//增加地址
export  const updateAddress = createAction(TYPES.UPDATE_ADDRESS)//增加地址
export  const setShopList = createAction(TYPES.SET_GOODS_LIST)//商品数据
//获取地址列表列表
export const getaddressList = (data)=>(dispatch,getState)=>{//本来应该是请求数据，测试
    console.log('getState',getState());
    data = data||{
        accountName: getState().Login_reducer.hks_userInfo.customeraccount.accountName
    }
    return ORDER.getAddress(data).then(res=>{
        if (res.data.errcode == 0) {
            const addList = res.data.consigneeofcustomers
            dispatch(addressList(addList))      
        }
    })
}

//增加地址
export const addAddressList = (data)=>(dispatch,getState)=>{
    
    return ORDER.addAddress(data).then(res=>{
        if (res.data.errcode == 0) {
            console.log('新增地址');
            
           dispatch(getaddressList())     
        }
    })
}
//删除地址
export const delAddressList = (data)=>(dispatch,getState)=>{
    
    return ORDER.delAddress(data).then(res=>{
        if (res.data.errcode == 0) {
            dispatch(getaddressList())     
        }
    })
}
//修改地址
export const updateAddressList = (data)=>(dispatch,getState)=>{
    
    return ORDER.editAddress(data).then(res=>{
        if (res.data.errcode == 0) {
            dispatch(getaddressList())      
        }
    })
}

