import React, { Component } from "react";
import PartItem from "./PartItem";
class CompanyItem extends Component {
    render() {
        const {companyInfo,index,purchaseDescribe,handlePurchaseDescribe}= this.props 
        console.log(index,purchaseDescribe);
        const {partItems,supplieName}  =companyInfo
        let totalAmount=0
        const items = partItems.map((v,i)=>{
            totalAmount+=(v.price*v.quantity)
            return <PartItem partInfo={v} key={i}/>
        })
        return (
            <div className="companyItem">
                <div className="header">
                    <span>{supplieName}</span>
                </div>
                <div>
                   {items}
                </div>
                <div className = "footer">
                    <div className="item">
                        <label>
                            留言：
                            <input type="text" value={purchaseDescribe} onChange={(e)=>{
                                handlePurchaseDescribe(index,e)
                            }}/>
                        </label>
                    </div>
                    <div className="item">
                        <span className = "fr warn subamount">
                            小计 ￥{totalAmount.toFixed(2)}
                        </span>
                        <span className = "fr subtotal">
                            共{partItems.length}件商品
                        </span>
                    </div>
                </div>
            </div>
        );
    }
}

export default CompanyItem;
